import {unreachable} from "@dynamo/common"
import React from "react"
import {FormFileUploadItem} from "./file-upload"
import {FormItem} from "./form"
import {FormLocationItem} from "./location"
import {FormSelectItem} from "./select"
import {FormSliderItem} from "./slider"
import {FormTextItem} from "./text"

export type FormItemResolverComponentProps = FormItem & {
    form: unknown
    handleChange: (id: string, value: unknown) => void
}
export const FormItemResolverComponent: React.FC<FormItemResolverComponentProps> = (
    formItem,
) => {
    let element: JSX.Element
    switch (formItem.type) {
        case "text":
            element = <FormTextItem {...formItem} />
            break
        case "select":
            element = <FormSelectItem {...formItem} />
            break
        case "slider":
            element = <FormSliderItem {...formItem} />
            break
        case "location":
            element = <FormLocationItem {...formItem} />
            break
        case "file-upload":
            element = <FormFileUploadItem {...formItem} />
            break
        case "custom":
            element = formItem.render(formItem)
            break
        default:
            unreachable("impossible case")
    }

    return <div>{element}</div>
}
