export interface LocationSearch {
    features: LocationFeature[]
    licence: string
    type: string
}

export interface LocationFeature {
    bbox?: number[]
    geometry?: Geometry
    properties: Properties
    type?: FeatureType
}

export interface Geometry {
    coordinates: number[]
    type: GeometryType
}

export enum GeometryType {
    Point = "Point",
}

export interface Properties {
    category?: Category
    display_name: string
    icon?: string
    importance?: number
    osm_id?: number
    osm_type?: OsmType
    place_id?: number
    place_rank?: number
    type?: string
}

export enum Category {
    Boundary = "boundary",
    Place = "place",
    Railway = "railway",
}

export enum OsmType {
    Node = "node",
    Relation = "relation",
    Way = "way",
}

export enum FeatureType {
    Feature = "Feature",
}

export const searchLocations = async (
    query: string,
): Promise<LocationSearch> => {
    const response = await fetch(
        `https://nominatim.openstreetmap.org/search?q=${encodeURI(
            query,
        )}&format=geojson`,
    )
    return response.json()
}
