import {IconButton} from "@material-ui/core"
import DeleteIcon from "@material-ui/icons/Delete"
import ReplayIcon from "@material-ui/icons/Replay"
import hash from "object-hash"
import React, {useEffect} from "react"
import {IPreviewProps} from "react-dropzone-uploader"
import {useContainer} from "unstated-next"
import {removeNested, renderFileType} from "../services/utils"
import {FileRender, FileRenderStateContainer} from "./file-renderer"

export const FilePreview = ({
    canCancel,
    canRemove,
    canRestart,
    className,
    extra: {multiple},
    fileWithMeta: {
        cancel,
        file,
        meta: {type},
        remove,
        restart,
    },
    files,
    handleChange,
    id,
    meta,
    style,
}: IPreviewProps & {
    handleChange: (value: File[] | File | undefined) => void
    id: string
}) => {
    const {name, percent, status} = meta
    const renderType = renderFileType(name)
    const {changePreviewImage} = useContainer(FileRenderStateContainer)
    const hashName = hash(`${file.name}-${file.size}`)
    useEffect(() => {
        if (status === "done") {
            const allFiles = files.map((file) => file.file)
            handleChange(multiple ? allFiles : allFiles[0])
        }
    }, [files, handleChange, multiple, status])

    return (
        <div className={className} style={style}>
            {renderType && (
                <FileRender
                    id={id}
                    name={hashName}
                    width={200}
                    height={200}
                    type={renderType}
                    source={
                        type.startsWith("image/")
                            ? {type: "url", url: URL.createObjectURL(file)}
                            : {file, type: "file"}
                    }
                />
            )}
            {name}
            <div className="dzu-previewStatusContainer">
                {status !== "done" && (
                    <progress
                        max={100}
                        value={status === "headers_received" ? 100 : percent}
                    />
                )}

                {status === "uploading" && canCancel && (
                    <IconButton
                        color="primary"
                        onClick={cancel}
                        aria-label="cancel">
                        <DeleteIcon />
                    </IconButton>
                )}
                {status !== "preparing" &&
                    status !== "getting_upload_params" &&
                    status !== "uploading" &&
                    canRemove && (
                        <IconButton
                            color="primary"
                            onClick={() => {
                                if (files.length === 1) {
                                    handleChange(multiple ? [] : undefined)
                                }
                                changePreviewImage((old) => {
                                    return (
                                        removeNested(
                                            old,
                                            `${id}.${hashName}`,
                                        ) as never
                                    )
                                })
                                remove()
                            }}
                            aria-label="remove">
                            <DeleteIcon />
                        </IconButton>
                    )}
                {[
                    "error_upload_params",
                    "exception_upload",
                    "error_upload",
                    "aborted",
                    "ready",
                ].includes(status) &&
                    canRestart && (
                        <IconButton
                            color="primary"
                            onClick={restart}
                            aria-label="restart">
                            <ReplayIcon />
                        </IconButton>
                    )}
            </div>
        </div>
    )
}
