import {ButtonProps, Fade, makeStyles, Typography} from "@material-ui/core"
import AccountCircleIcon from "@material-ui/icons/AccountCircle"
import LockIcon from "@material-ui/icons/Lock"
import PersonAddIcon from "@material-ui/icons/PersonAdd"
import React, {useEffect, useState} from "react"
import {NotifyMessage} from "../client"
import {useMeQuery, User, UserType} from "../graphql/schema.generated"
import {InvitePage} from "../pages/invite"
import {goTo} from "../services/utils"
import {HideAppBar, SideBarButton} from "./hide-app-bar"
import {MenuComponent} from "./menu"
import {ModalComponent} from "./modal"
import {SnackBarComponent} from "./snackbar"

export interface RolePage {
    component?: (user: User) => React.ReactNode
    redirect?: string
    role: UserType
    title?: string
}

const useStyles = makeStyles(() => ({
    container: {
        display: "flex",
        flexDirection: "column",
    },
    footer: {
        marginTop: "7vh",
        textAlign: "center",
    },
}))

export interface PageComponentProps {
    disableHide?: boolean
    rolePages?: RolePage[]
    roleRedirect?: string
    showFooter?: boolean
    title: string
}
export const PageComponent: React.FunctionComponent<PageComponentProps> = ({
    children,
    disableHide = true,
    rolePages,
    roleRedirect,
    showFooter = false,
    title,
}) => {
    const classes = useStyles()
    const [snack, updateSnack] = useState<NotifyMessage>()

    useEffect(() => {
        document.title = `Op Dynamo | ${title}`
    }, [title])

    const [component, changeComponent] = useState<React.ReactNode>()
    const {data: meData, error: meError, loading} = useMeQuery()

    const alwaysButtons = [
        {
            key: "loggedOutLegal",
            path: "/legal",
            title: "Legal",
            type: "bar-button",
        },
        {
            key: "loggedOutAbout",
            path: "/about",
            title: "About",
            type: "bar-button",
        },
        {
            key: "loggedOutContact",
            path: "/contact",
            title: "Contact",
            type: "bar-button",
        },
        {key: "loggedOutFAQ", path: "/faq", title: "FAQ"},
    ]

    useEffect(() => {
        if (!meError && rolePages && meData?.me?.user) {
            const userType = meData.me.user.type
            const rolePage = rolePages.find((rolePage) => {
                return rolePage.role === userType
            })
            if (!rolePage) {
                if (roleRedirect) {
                    goTo(roleRedirect)
                }
            } else {
                if (rolePage.component) {
                    changeComponent(rolePage.component(meData.me.user))
                }
                if (rolePage.redirect) {
                    goTo(rolePage.redirect)
                }
            }
        } else if (!loading) {
            changeComponent(children)
        }
    }, [rolePages, meData, meError, roleRedirect, children, loading])

    return (
        <div>
            <HideAppBar
                disableHide={disableHide}
                inverted={false}
                homeButton={{
                    path: "/",
                    title: "Operation Dynamo",
                    type: "bar-button",
                }}
                buttons={
                    [
                        ...alwaysButtons,
                        ...(meData?.me?.user
                            ? [
                                  {
                                      element: (
                                          <ModalComponent
                                              title="Invite"
                                              buttonProps={{
                                                  startIcon: <PersonAddIcon />,
                                                  style: {marginRight: 10},
                                              }}>
                                              {({closeModal}) =>
                                                  meData?.me?.user && (
                                                      <InvitePage
                                                          user={
                                                              meData?.me?.user
                                                          }
                                                          closeModal={
                                                              closeModal
                                                          }
                                                      />
                                                  )
                                              }
                                          </ModalComponent>
                                      ),
                                      key: "invite",
                                      type: "element",
                                  },

                                  {
                                      element: (
                                          <MenuComponent
                                              name={`${meData.me.user.firstName} ${meData.me.user.lastName}`}
                                              icon={<AccountCircleIcon />}
                                              options={[
                                                  {
                                                      key: "update",
                                                      name: "Update Account",
                                                      url: "/account/update",
                                                  },
                                                  {
                                                      key: "logout",
                                                      name: "Logout",
                                                      url: "/account/logout",
                                                  },
                                              ]}
                                          />
                                      ),
                                      key: "loggedIn",
                                      type: "element",
                                  },
                              ]
                            : [
                                  {
                                      key: "loggedOutLogin",
                                      path: "/account/login",
                                      props: {
                                          color: "primary",
                                          startIcon: <LockIcon />,
                                          style: {
                                              color: "white",
                                              height: 35,
                                              marginLeft: 10,
                                          },
                                          variant: "contained",
                                      } as ButtonProps,
                                      title: "Login",
                                      type: "bar-button",
                                  },
                              ]),
                    ] as ((SideBarButton & {
                        element: JSX.Element
                        type: "element"
                    }) & {key: string})[]
                }>
                <div style={{marginTop: 40}}>{component}</div>
            </HideAppBar>
            <footer className={classes.footer}>
                {showFooter ? (
                    <Fade timeout={1000} in={showFooter}>
                        <Typography variant="caption" component="h3">
                             Operation Dynamo A Creation of Conva LLC. All
                            Rights Reserved
                        </Typography>
                    </Fade>
                ) : null}
            </footer>
            <SnackBarComponent state={[snack, updateSnack]} />
        </div>
    )
}
