import {Button} from "@material-ui/core"
import Menu from "@material-ui/core/Menu"
import MenuItem from "@material-ui/core/MenuItem"
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown"
import React from "react"
import {goTo} from "../services/utils"

const ITEM_HEIGHT = 48

export interface MenuComponentProps {
    icon: JSX.Element
    name: string
    options: {key: string; name: string; url: string}[]
}
export const MenuComponent: React.FC<MenuComponentProps> = ({
    icon,
    name,
    options,
}) => {
    const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null)
    const open = Boolean(anchorEl)

    const handleClick = (event: React.MouseEvent<HTMLElement, MouseEvent>) => {
        setAnchorEl(event.currentTarget)
    }

    const handleClose = (url?: string) => {
        setAnchorEl(null)
        if (url) {
            goTo(url)
        }
    }

    return (
        <Button
            variant="contained"
            color="primary"
            onClick={(event) => {
                if (!open) handleClick(event)
            }}
            endIcon={<KeyboardArrowDownIcon />}
            startIcon={icon}>
            {name}

            <Menu
                id="long-menu"
                anchorEl={anchorEl}
                keepMounted
                open={open}
                onClose={() => {
                    handleClose()
                }}
                PaperProps={{
                    style: {
                        maxHeight: ITEM_HEIGHT * 4.5,
                        width: "20ch",
                    },
                }}>
                {options.map(({key, name, url}) => (
                    <MenuItem onClick={() => handleClose(url)} {...{key}}>
                        {name}
                    </MenuItem>
                ))}
            </Menu>
        </Button>
    )
}
