import {NormalizedCacheObject} from "apollo-cache-inmemory"
import ApolloClient from "apollo-client"
import {cachedStorage} from "."
import {
    LoginRefreshTokenDocument,
    LoginRefreshTokenMutation,
    LoginRefreshTokenMutationVariables,
} from "../graphql/schema.generated"

const decode = (jwt: string): {exp?: number} | undefined =>
    jwt && JSON.parse(atob(jwt.split(".")[1]))

const tokenExpired = (jwt: string) =>
    (decode(jwt)?.exp ?? 0) * 1000.0 <= Date.now()

const getNewToken = async (client: ApolloClient<NormalizedCacheObject>) => {
    const [userId, refreshToken] = [
        cachedStorage.getItem("USER_ID"),
        cachedStorage.getItem("REFRESH_TOKEN"),
    ] as const
    if (!userId || !refreshToken) {
        // logout()
        return ""
    }

    const {data, errors} = await client.mutate<
        LoginRefreshTokenMutation,
        LoginRefreshTokenMutationVariables
    >({
        mutation: LoginRefreshTokenDocument,
        variables: {refreshToken, userId},
    })
    if (errors || !data?.loginRefreshToken?.token) {
        // logout()
        return ""
    }

    cachedStorage.setItem("JWT_TOKEN", data.loginRefreshToken.token)
    return data?.loginRefreshToken?.token
}

export const getJwtToken = async (
    client: ApolloClient<NormalizedCacheObject>,
) => {
    const jwtToken = cachedStorage.getItem("JWT_TOKEN")
    if (!jwtToken || tokenExpired(jwtToken)) {
        return await getNewToken(client)
    }
    return jwtToken
}
