/* eslint-disable no-return-assign */
let cache: Record<string, string | null> = {}

type KeyType = "JWT_TOKEN" | "REFRESH_TOKEN" | "USER_ID"

export const cachedStorage = {
    clear: () => {
        cache = {}
        localStorage.clear()
    },
    getItem: (key: KeyType) =>
        cache[key] ?? (cache[key] = localStorage.getItem(key)),
    removeItem: (key: KeyType) => {
        delete cache[key]
        localStorage.removeItem(key)
    },
    setItem: (key: KeyType, value: string) => {
        cache[key] = value
        localStorage.setItem(key, value)
    },
} as const
