import {assert, unreachable} from "@dynamo/common"
import {makeStyles} from "@material-ui/core"
import Avatar from "@material-ui/core/Avatar"
import Button from "@material-ui/core/Button"
import CardContent from "@material-ui/core/CardContent"
import CardHeader from "@material-ui/core/CardHeader"
import CardMedia from "@material-ui/core/CardMedia"
import {red} from "@material-ui/core/colors"
import LinearProgress from "@material-ui/core/LinearProgress"
import Typography from "@material-ui/core/Typography"
import React, {useCallback, useMemo} from "react"
import {CardItem} from "../components/card-item"
import {GridView} from "../components/file-item-preview"
import {PageComponent} from "../components/page"
import {PageTitleBar} from "../components/page-title-bar"
import {WrongPageComponent} from "../components/wrong-page"
import {Job, useAcceptedJobsQuery} from "../graphql/schema.generated"
import {goTo} from "../services/utils"
import {useDateFromNow} from "../util"

const useStyles = makeStyles((theme) => ({
    avatar: {
        backgroundColor: red[500],
    },
    cardRoot: {
        margin: 20,
        width: 345,
    },
    expand: {
        marginLeft: "auto",
        transform: "rotate(0deg)",
        transition: theme.transitions.create("transform", {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: "rotate(180deg)",
    },

    media: {
        height: 0,
        paddingTop: "56.25%", // 16:9
    },
    progressBar: {
        marginBottom: 10,
        marginLeft: -16,
        marginRight: -16,
        marginTop: -16,
    },

    progressText: {fontWeight: "bold"},
}))

export interface JobCardProps {
    job: Job
    showAvatar?: boolean
}
export const JobCard: React.FC<JobCardProps> = ({job, showAvatar}) => {
    const classes = useStyles()

    const onClick = useCallback(() => {
        goTo(`/job?id=${job.id}`)
    }, [job.id])

    const createdAt = useDateFromNow(job.createdAt)
    const progressNumber = useMemo(() => {
        switch (job.progress) {
            case "Assigned":
                return 0
            case "PrintComplete":
                return 0.5
            case "PrintStarted":
                return 0.25
            case "Shipped":
                return 0.75
            case "Received":
                return 1.0
            default:
                unreachable(`Invalid job progress`)
        }
    }, [job.progress])

    return (
        <CardItem onClick={onClick}>
            <div>
                <CardHeader
                    avatar={
                        showAvatar && (
                            <Avatar
                                alt={`${job.volunteer.firstName} ${job.volunteer.lastName}`}
                                className={classes.avatar}>
                                {job.volunteer.firstName[0]}
                            </Avatar>
                        )
                    }
                    title={job.name}
                    subheader={createdAt}
                />
                {job.submission.files.preview && (
                    <CardMedia
                        className={classes.media}
                        image={job.submission.files.preview.presignedUrl}
                        title={job.name}
                    />
                )}
                <CardContent>
                    <LinearProgress
                        className={classes.progressBar}
                        variant="determinate"
                        value={progressNumber * 100}
                    />

                    <Typography
                        className={classes.progressText}
                        variant="body2"
                        color="textSecondary"
                        component="p">
                        {`Progress: ${job.progress} (${(
                            progressNumber * 100
                        ).toFixed(0)}%)`}
                    </Typography>

                    <Typography
                        variant="body2"
                        color="textSecondary"
                        component="p">
                        {`Quantity Accepted: ${job.quantity}`}
                    </Typography>

                    {job.description && (
                        <Typography
                            variant="body2"
                            color="textSecondary"
                            component="p">
                            {job.description}
                        </Typography>
                    )}
                </CardContent>
            </div>
        </CardItem>
    )
}

export interface VolunteerJobsProps {}
export const VolunteerJobs: React.FC<VolunteerJobsProps> = () => {
    const {data, loading} = useAcceptedJobsQuery()
    if (loading) {
        return <div>Loading...</div>
    }
    assert(data?.acceptedJobs, `Couldn't get accepted jos!`)

    return (
        <>
            <PageTitleBar
                title="My Accepted Jobs"
                rightItem={
                    <Button
                        color="primary"
                        onClick={() => {
                            goTo("/find")
                        }}
                        variant="contained">
                        Find Jobs
                    </Button>
                }
            />

            <GridView>
                {data.acceptedJobs
                    .sort(
                        (a, b) =>
                            new Date(b.createdAt).getTime() -
                            new Date(a.createdAt).getTime(),
                    )
                    .map((job) => (
                        <JobCard key={job.id} {...{job}} />
                    ))}
            </GridView>
        </>
    )
}

export interface JobsPageProps {}
export const JobsPage: React.FC<JobsPageProps> = () => (
    <div>
        <PageComponent
            disableHide={false}
            showFooter
            title="Home"
            rolePages={[
                {
                    component: () => <VolunteerJobs />,
                    role: "Volunteer",
                },
            ]}>
            <WrongPageComponent text="Must be signed in to view submissions" />
        </PageComponent>
        <br />
    </div>
)
