import {Button, Grid, makeStyles} from "@material-ui/core"
import GetAppIcon from "@material-ui/icons/GetApp"
import React from "react"
import {File} from "../graphql/schema.generated"
import {goTo} from "../services/utils"
import {FileRender, FileRenderStateContainer, RenderType} from "./file-renderer"

const useStyles = makeStyles(() => ({
    gridContainer: {
        margin: 5,
    },
}))

export interface GridViewProps {
    children: React.ReactNode[]
}
export const GridView: React.FC<GridViewProps> = ({children}) => {
    const classes = useStyles()

    return (
        <div className={classes.gridContainer}>
            <Grid container spacing={1}>
                {children.map((child, i) => (
                    // eslint-disable-next-line react/no-array-index-key
                    <Grid key={i} item>
                        {child}
                    </Grid>
                ))}
            </Grid>
        </div>
    )
}

export interface FileItemPreviewProps {
    items: {file: File; type: RenderType}[]
}
export const FileItemPreview: React.FC<FileItemPreviewProps> = ({items}) => {
    return (
        <div style={{display: "flex", justifyContent: "center"}}>
            <GridView>
                {items.map(({file: {id, presignedUrl}, type}) => (
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            margin: 10,
                        }}
                        key={id}>
                        <FileRenderStateContainer.Provider>
                            <FileRender
                                type={type}
                                width={200}
                                height={200}
                                source={{type: "url", url: presignedUrl}}
                            />
                        </FileRenderStateContainer.Provider>

                        <Button
                            variant="contained"
                            style={{height: 35}}
                            color="primary"
                            onClick={() => goTo(presignedUrl)}
                            startIcon={<GetAppIcon />}>
                            {`Download ${type.toUpperCase()}`}
                        </Button>
                    </div>
                ))}
            </GridView>
        </div>
    )
}
