import * as React from "react"
import {useState} from "react"
import {MarkdownComponent} from "../../components/markdown"
import {PageComponent} from "../../components/page"

export interface TermsOfServicePageProps {}
export const TermsOfServicePage: React.FC<TermsOfServicePageProps> = () => {
    const [showFooter, changeShowFooter] = useState<boolean>()
    return (
        <PageComponent
            disableHide={false}
            showFooter={showFooter}
            title="Terms of Service">
            <MarkdownComponent
                onFinish={() => changeShowFooter(true)}
                url="terms-of-service.md"
            />
        </PageComponent>
    )
}
