import {
    Button,
    ButtonProps,
    createStyles,
    Fade,
    makeStyles,
} from "@material-ui/core"
import AppBar from "@material-ui/core/AppBar"
import CssBaseline from "@material-ui/core/CssBaseline"
import Link from "@material-ui/core/Link"
import Toolbar from "@material-ui/core/Toolbar"
import useScrollTrigger from "@material-ui/core/useScrollTrigger"
import React, {useEffect, useState} from "react"
import {theme} from "./theme"

const useStyles = makeStyles(() =>
    createStyles({
        link: {
            textDecoration: "none",
        },
        sidebarButtons: {
            display: "flex",
            justifyContent: "flex-start",
        },
    }),
)

export interface SideBarButton {
    path: string
    props?: ButtonProps
    title: string
    type: "bar-button"
}

let previousScroll = 0

export interface HideAppBarProps {
    buttons: ((SideBarButton | {element: JSX.Element; type: "element"}) & {
        key: string
    })[]
    disableHide: boolean
    homeButton: SideBarButton
    inverted: boolean
    window?: () => Window
}

interface HideOnScorllProps extends HideAppBarProps {
    throttleInverted: (inverted: boolean) => void
}
const HideOnScroll: React.FC<
    HideOnScorllProps & {
        children: React.ReactElement | undefined
    }
> = ({children, throttleInverted, window: window_prop}) => {
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.

    const threshold = 50
    const trigger = useScrollTrigger({
        target: window_prop ? window_prop() : undefined,
        threshold,
    })
    useEffect(() => {
        window.addEventListener("scroll", () => {
            if (window.scrollY <= 0) {
                throttleInverted(false)
            } else if (
                window.scrollY < previousScroll &&
                !trigger &&
                window.scrollY > threshold
            ) {
                throttleInverted(true)
            }
            previousScroll = window.scrollY
        })
    }, [throttleInverted, trigger])

    return <Fade in={!trigger}>{children}</Fade>
}

export const HideAppBar: React.FC<HideAppBarProps> = (props) => {
    const {
        buttons,
        children,
        disableHide,
        homeButton,
        inverted: invertedInitial,
    } = props

    const classes = useStyles()
    const [inverted, changeInverted] = useState(invertedInitial)
    const buttonStyle = {
        color: inverted ? "white" : theme.palette.primary.main,
        fontFamily: theme.typography.fontFamily,
        fontSize: 15,
        textTransform: "none" as "none",
    }

    const appBar = (
        <AppBar
            elevation={0}
            style={{
                background: inverted ? theme.palette.primary.main : "white",
            }}>
            <Toolbar className={classes.sidebarButtons}>
                <div style={{flexGrow: 1}}>
                    <Link href={homeButton.path}>
                        <Button style={buttonStyle} color="inherit">
                            {homeButton.title}
                        </Button>
                    </Link>
                </div>
                <div>
                    {buttons.map(({key, ...button}) => {
                        if (button.type === "element") {
                            return <Link {...{key}}>{button.element}</Link>
                        }
                        const {path, props, title} = button
                        return (
                            <Link {...{key}} href={path}>
                                <Button
                                    style={buttonStyle}
                                    color="inherit"
                                    {...props}>
                                    {title}
                                </Button>
                            </Link>
                        )
                    })}
                </div>
            </Toolbar>
        </AppBar>
    )

    return (
        <>
            <CssBaseline />
            {disableHide ? (
                <div>{appBar}</div>
            ) : (
                <HideOnScroll throttleInverted={changeInverted} {...props}>
                    {appBar}
                </HideOnScroll>
            )}

            <Toolbar />
            {children}
        </>
    )
}
