import {assert} from "@dynamo/common"
import {Button, Typography} from "@material-ui/core"
import Container from "@material-ui/core/Container"
import {makeStyles} from "@material-ui/core/styles"
import ImageIcon from "@material-ui/icons/Image"
import React, {useCallback, useMemo, useState} from "react"
import {createContainer, useContainer} from "unstated-next"
import {v4 as uuid} from "uuid"
import {FilePreview} from "../../components/file-preview"
import {FileRenderStateContainer} from "../../components/file-renderer"
import {FormComponent, FormItem} from "../../components/form/form"
import {InfoListComponent} from "../../components/info-list"
import {PageComponent} from "../../components/page"
import {TabsComponent} from "../../components/tabs"
import {WrongPageComponent} from "../../components/wrong-page"
import {
    MedicalItem,
    MutationCreateSubmissionArgs,
    SubmissionFilesInput,
    useCreateSubmissionMutation,
    useMedicalItemsQuery,
    User,
} from "../../graphql/schema.generated"
import {useSnack} from "../../hooks"
import {dataURLtoFile, getNested, goTo} from "../../services/utils"

export const SubmissionStateContainer = createContainer(() => {
    const [submission, changeSubmission] = useState<
        Partial<MutationCreateSubmissionArgs>
    >({})
    const [step, changeStep] = useState<"first" | "second">("first")
    return {
        changeStep,
        changeSubmission,
        step,
        submission,
    } as const
})

const useStyles = makeStyles((theme) => ({
    paper: {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        marginTop: theme.spacing(8),
    },
}))

export interface CreateSubmissionPageViewProps {
    medicalItems: MedicalItem[]
    onSubmit: (form: MutationCreateSubmissionArgs) => void
    user: User
}

const MAX_JOBS = 400

export interface SubmissionItemSelectProps {
    medicalItems: MedicalItem[]
}
export const SubmissionItemSelect: React.FC<SubmissionItemSelectProps> = ({
    medicalItems,
}) => {
    const {
        changeStep,
        changeSubmission,
        submission: {description, files, marketplaceId, name},
    } = useContainer(SubmissionStateContainer)

    const {updateSnack} = useSnack()
    const onMarketplaceSelect = useCallback(
        (id?: string) => {
            changeSubmission({
                marketplaceId: id,
                name: medicalItems.find((item) => item.id === id)?.name,
            })
            changeStep("second")
        },
        [changeStep, changeSubmission, medicalItems],
    )

    const onCustomSelect = useCallback(
        (custom: {description?: string; files: SubmissionFilesInput}) => {
            changeSubmission((old) => {
                return {...old, ...custom, marketplaceId: undefined}
            })
            changeStep("second")
        },
        [changeStep, changeSubmission],
    )

    const formItems = useMemo(() => {
        return [
            {
                PreviewComponent: FilePreview,
                accept: ".jpg,.JPG,.JPEG,.jpeg,.png,.PNG",
                alignment: {xs: 12},
                defaultValue: files?.preview,
                icon: <ImageIcon />,
                id: ".files.preview",
                label: "Upload Thumbnail",
                multiple: false,
                required: true,
                type: "file-upload",
            },

            {
                alignment: {xs: 12},
                defaultValue: name,
                id: ".name",
                label: "Name",
                required: true,
                type: "text",
            },

            {
                alignment: {xs: 12},
                defaultValue: description,
                id: ".description",
                label: "Description",
                multiline: true,
                type: "text",
            },

            {
                alignment: {xs: 12},
                defaultValue: files?.assembly,
                id: ".files.assemblyUrl",
                label: "Assembly Instructions (URL)",
                type: "text",
            },

            {
                PreviewComponent: FilePreview,
                accept: ".stl,.STL",
                alignment: {xs: 12},
                defaultValue: files?.model,
                id: ".files.model",
                label: "Upload STL (3D Printing)",
                required: true,
                type: "file-upload",
            },

            {
                PreviewComponent: FilePreview,
                accept: ".dxf,.DXF",
                alignment: {xs: 12},
                defaultValue: files?.laserCutting,
                id: ".files.laserCutting",
                label: "Upload DXF (Laser Cutting)",
                required: true,
                type: "file-upload",
            },
        ] as FormItem[]
    }, [description, files, name])

    return (
        <TabsComponent
            currentTab={!files || marketplaceId ? 0 : 1}
            tabs={[
                {
                    component: (
                        <InfoListComponent
                            items={medicalItems.map(
                                ({description, files: {preview}, id, name}) => {
                                    return {
                                        action: {
                                            callback: onMarketplaceSelect,
                                            type: "callback",
                                        },
                                        description,
                                        id,
                                        image: preview?.presignedUrl,
                                        name,
                                    }
                                },
                            )}
                        />
                    ),
                    name: "Frequent",
                },
                {
                    component: (
                        <FormComponent
                            submitButtonTitle="Create Submission"
                            formItems={formItems}
                            onSubmit={(
                                form: Partial<MutationCreateSubmissionArgs>,
                            ) => {
                                if (
                                    (form.files?.laserCutting &&
                                        form.files.laserCutting.length >= 1) ||
                                    (form.files?.model &&
                                        form.files.model.length >= 1)
                                ) {
                                    onCustomSelect(
                                        form as {
                                            description?: string
                                            files: SubmissionFilesInput
                                        },
                                    )
                                } else {
                                    updateSnack({
                                        message:
                                            "You must upload an STL or DXF file to proceed",
                                    })
                                }
                            }}
                        />
                    ),
                    name: "Custom",
                },
            ]}
        />
    )
}

export const CreateSubmissionPageView: React.FC<CreateSubmissionPageViewProps> = ({
    medicalItems,
    onSubmit,
    user: {address, latitude, longitude},
}) => {
    const classes = useStyles()
    const {changeStep, step, submission} = useContainer(
        SubmissionStateContainer,
    )

    const {previewImage} = useContainer(FileRenderStateContainer)

    const submitFormItems: FormItem[] = [
        {
            alignment: {xs: 12},
            defaultValue: 85,
            ending: "Parts",
            id: ".quantity",
            label: "Quantity",
            max: MAX_JOBS,
            min: 5,
            step: 1,
            type: "slider",
        },

        {
            alignment: {xs: 12},
            defaultValue: {
                geometry: {
                    coordinates: [longitude, latitude],
                },
                properties: {
                    display_name: address,
                },
            },
            id: ".address",
            label: "Shipping Address",
            locationIds: {
                latitude: ".geolocation.latitude",
                longitude: ".geolocation.longitude",
            },
            required: true,
            type: "location",
        },
    ]

    const findItemFromId = useCallback(
        (id: string) => {
            return medicalItems.find((medicalItem) => medicalItem.id === id)
        },
        [medicalItems],
    )

    const stepViews = useMemo(() => {
        switch (step) {
            case "first":
                return <SubmissionItemSelect medicalItems={medicalItems} />
            case "second": {
                const createdSubmission = submission.marketplaceId
                    ? findItemFromId(submission.marketplaceId)
                    : submission

                assert(createdSubmission, "submission not defined")
                const {description, files, name} = createdSubmission

                const previewPreview = Object.values(
                    getNested(previewImage || {}, ".files.preview") || {},
                )[0] as string

                const modelPreview = Object.values(
                    getNested(previewImage || {}, ".files.model") || {},
                )[0] as string

                const laserCuttingPreview = Object.values(
                    getNested(previewImage || {}, ".files.laserCutting") || {},
                )[0] as string

                return (
                    <div
                        style={{
                            marginTop: 10,
                            maxWidth: 1000,
                            minWidth: 200,
                            width: "45vw",
                        }}>
                        <InfoListComponent
                            items={[
                                {
                                    action: {
                                        callback: () => {
                                            changeStep("first")
                                        },
                                        type: "callback",
                                    },
                                    actionItem: (
                                        <Button
                                            variant="outlined"
                                            onClick={() => {
                                                changeStep("first")
                                            }}>
                                            Edit
                                        </Button>
                                    ),
                                    description: description || "",
                                    id: submission.marketplaceId || uuid(),
                                    image:
                                        files?.preview?.presignedUrl ||
                                        previewPreview ||
                                        modelPreview ||
                                        laserCuttingPreview,
                                    name: name || "",
                                },
                            ]}
                        />

                        <FormComponent
                            submitButtonTitle="Create Submission"
                            formItems={submitFormItems}
                            onSubmit={(form: {}) => {
                                const submitted = {
                                    ...submission,
                                    ...form,
                                } as MutationCreateSubmissionArgs

                                if (submitted.files) {
                                    if (!submitted.files?.laserCutting) {
                                        submitted.files.laserCutting = []
                                    }

                                    if (!submitted.files?.model) {
                                        submitted.files.model = []
                                    }
                                }

                                if (submitted.files) {
                                    if (!submitted.files.preview) {
                                        submitted.files.preview = dataURLtoFile(
                                            modelPreview ||
                                                laserCuttingPreview ||
                                                "",
                                            "thumbnail.png",
                                        )
                                    }
                                }

                                onSubmit(submitted)
                            }}
                        />
                    </div>
                )
            }
        }
    }, [
        changeStep,
        findItemFromId,
        medicalItems,
        onSubmit,
        previewImage,
        step,
        submission,
        submitFormItems,
    ])

    return (
        <Container component="main" maxWidth="xs">
            <div className={classes.paper}>
                <Typography component="h1" variant="h5">
                    Create Medical Submission
                </Typography>
                <div
                    style={{
                        maxWidth: 1000,
                        minWidth: 200,
                        width: "45vw",
                    }}
                />
                {stepViews}
            </div>
        </Container>
    )
}

export interface CreateSubmissionPageProps {}
export const CreateSubmissionPage: React.FC<CreateSubmissionPageProps> = () => {
    const {updateSnack} = useSnack()
    const {data} = useMedicalItemsQuery({
        onError: ({message}) => {
            updateSnack({message})
        },
    })

    const [mutate] = useCreateSubmissionMutation({
        onCompleted: ({createSubmission: {id}}) => {
            goTo(`/submission/?id=${id}`)
        },
        onError: ({message}) => {
            updateSnack({message})
        },
    })
    const onSubmit = useCallback(
        (variables: MutationCreateSubmissionArgs) => {
            mutate({variables})
        },
        [mutate],
    )

    return (
        <SubmissionStateContainer.Provider>
            <PageComponent
                showFooter
                title="Create Job"
                rolePages={[
                    {
                        component: (user) => (
                            <div>
                                {data?.medicalItems && (
                                    <FileRenderStateContainer.Provider>
                                        <CreateSubmissionPageView
                                            medicalItems={data.medicalItems}
                                            {...{onSubmit, user}}
                                        />
                                    </FileRenderStateContainer.Provider>
                                )}
                            </div>
                        ),
                        role: "Submitter",
                    },
                ]}>
                <WrongPageComponent text="Must be logged in as a Submitter to create a job" />
            </PageComponent>
        </SubmissionStateContainer.Provider>
    )
}
