import {createStyles, makeStyles} from "@material-ui/core"
import * as React from "react"
import {PictureButton} from "./picture-button"

const useStyles = makeStyles(() =>
    createStyles({
        root: {
            display: "flex",
            flexWrap: "wrap",
            minWidth: 300,
            width: "100%",
        },
    }),
)

export interface PictureSelectComponentProps {
    images: {
        key: string
        onClick: () => void
        title: string
        url: string
        width: string
    }[]
}
export const PictureSelectComponent: React.FC<PictureSelectComponentProps> = ({
    images,
}) => {
    const classes = useStyles()

    return (
        <div>
            <div style={{marginTop: 20, textAlign: "center"}}>
                <div className={classes.root}>
                    {images.map((image) => (
                        <PictureButton {...image} />
                    ))}
                </div>
            </div>
        </div>
    )
}
