import {Helper} from "dxf"
import {theme} from "../components/theme"

export const dxfToImageBuffer = ({
    dxf,
    strokeColor = theme.palette.primary.main,
    strokeWidth = "0.2%",
}: {
    dxf: string
    strokeColor?: string
    strokeWidth?: string
}) => {
    const helper = new Helper(dxf)
    const svg = helper
        .toSVG()
        .replace(`stroke-width="0.1%"`, `stroke-width="${strokeWidth}"`)
        .replace(`stroke="rgb(0, 0, 0)"`, `stroke="${strokeColor}"`)

        .replace(`stroke="#000000"`, `stroke="${strokeColor || "#000000"}"`)

    const buff = Buffer.from(svg)
    return buff
}
