import {ApolloProvider} from "@apollo/react-hooks"
import React, {useState} from "react"
import {BrowserRouter, Route, Switch} from "react-router-dom"
import {client, NotifyMessage} from "./client"
import {SnackBarComponent} from "./components/snackbar"
import {SnackContext} from "./hooks/useSnack"
import {IndexPage} from "./pages"
import {AboutPage} from "./pages/about"
import {AccountChoosePage} from "./pages/account/choose"
import {ForgotPasswordPage} from "./pages/account/forgot-password"
import {LoginPage} from "./pages/account/login"
import {LogoutPage} from "./pages/account/logout"
import {PasswordResetPage} from "./pages/account/password-reset"
import {RegisterPage} from "./pages/account/register"
import {AccountUpdatePage} from "./pages/account/update"
import {ContactPage} from "./pages/contact"
import {FindPage} from "./pages/find"
import {JobPage} from "./pages/job"
import {LegalPage} from "./pages/legal"
import {DisclaimerPage} from "./pages/legal/disclaimer"
import {FaqPage} from "./pages/legal/faq"
import {PrivacyPolicyPage} from "./pages/legal/privacy-policy"
import {TermsOfServicePage} from "./pages/legal/terms-of-service"
import {SubmissionPage} from "./pages/submission"
import {CreateSubmissionPage} from "./pages/submission/create"

export interface AppViewProps {}
export const AppView: React.FC<AppViewProps> = () => {
    const [snack, updateSnack] = useState<NotifyMessage>()

    return (
        <SnackContext.Provider value={{updateSnack}}>
            <div className="App">
                <BrowserRouter>
                    <Switch>
                        <Route path="/submission/create">
                            <CreateSubmissionPage />
                        </Route>

                        <Route path="/submission">
                            <SubmissionPage />
                        </Route>

                        <Route path="/find">
                            <FindPage />
                        </Route>

                        <Route path="/legal/terms-of-service">
                            <TermsOfServicePage />
                        </Route>

                        <Route path="/legal/privacy-policy">
                            <PrivacyPolicyPage />
                        </Route>

                        <Route path="/faq">
                            <FaqPage />
                        </Route>

                        <Route path="/legal/disclaimer">
                            <DisclaimerPage />
                        </Route>

                        <Route path="/account/logout">
                            <LogoutPage />
                        </Route>

                        <Route path="/account/password-reset">
                            <PasswordResetPage />
                        </Route>

                        <Route path="/account/forgot-password">
                            <ForgotPasswordPage />
                        </Route>

                        <Route path="/account/update">
                            <AccountUpdatePage />
                        </Route>

                        <Route path="/account/register/choose">
                            <AccountChoosePage />
                        </Route>

                        <Route path="/job">
                            <JobPage />
                        </Route>

                        <Route path="/account/register">
                            <RegisterPage />
                        </Route>

                        <Route path="/account/login">
                            <LoginPage />
                        </Route>

                        <Route path="/about">
                            <AboutPage />
                        </Route>

                        <Route path="/legal">
                            <LegalPage />
                        </Route>

                        <Route path="/contact">
                            <ContactPage />
                        </Route>

                        <Route path="/">
                            <IndexPage />
                        </Route>
                    </Switch>
                </BrowserRouter>

                <SnackBarComponent state={[snack, updateSnack]} />
            </div>
        </SnackContext.Provider>
    )
}

export interface AppProps {}
export const App: React.FC<AppProps> = () => (
    <ApolloProvider {...{client}}>
        <AppView />
    </ApolloProvider>
)
