import React, {useCallback, useMemo} from "react"
import {FormComponent, FormItem} from "../components/form/form"
import {
    MutationSendInvitationArgs,
    User,
    useSendInvitationMutation,
} from "../graphql/schema.generated"
import {useSnack} from "../hooks"

export interface InvitePageViewProps {
    onSubmit: (args: MutationSendInvitationArgs) => void
    user: User
}
export const InvitePageView: React.FC<InvitePageViewProps> = ({
    onSubmit,
    user,
}) => {
    const formItems = useMemo(
        () =>
            [
                {
                    alignment: {xs: 12},
                    defaultValue: user.type,
                    disabled: user.type !== "Submitter",
                    id: ".type",
                    label: "User Type",
                    options: {
                        Submitter: "Medical",
                        Volunteer: "Maker",
                    },
                    required: true,
                    type: "select",
                },

                {
                    alignment: {xs: 12},
                    autoComplete: "email",
                    id: ".email",
                    label: "Email",
                    required: true,
                    textType: "email",
                    type: "text",
                },
            ] as FormItem[],
        // eslint-disable-next-line react-hooks/exhaustive-deps
        [],
    )

    return (
        <FormComponent
            submitButtonTitle="Invite"
            formItems={formItems}
            onSubmit={onSubmit}
        />
    )
}

export interface InvitePageProps {
    closeModal: () => void
    user: User
}
export const InvitePage: React.FC<InvitePageProps> = ({closeModal, user}) => {
    const {updateSnack} = useSnack()
    const [mutate] = useSendInvitationMutation({
        onCompleted: (data) => {
            if (data.sendInvitation) {
                updateSnack({
                    message: "successfully sent invitation",
                    severity: "success",
                })
                closeModal()
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
    })

    const onSubmit = useCallback(
        (args: MutationSendInvitationArgs) => mutate({variables: args}),
        [mutate],
    )

    return <InvitePageView user={user} onSubmit={onSubmit} />
}
