export interface SubmissionCardProps {
    showAvatar?: boolean
    submission: Submission
}
export const SubmissionCard: React.FC<SubmissionCardProps> = ({
    showAvatar = false,
    submission,
}) => {
    const classes = useStyles()

    const onClick = useCallback(() => {
        goTo(`/submission?id=${submission.id}`)
    }, [submission.id])

    const createdAt = useDateFromNow(submission.createdAt)
    return (
        <CardItem onClick={onClick}>
            <CardHeader
                avatar={
                    showAvatar && (
                        <Avatar
                            alt={`${submission.submitter.firstName} ${submission.submitter.lastName}`}
                            className={classes.avatar}>
                            {submission.submitter.firstName[0]}
                        </Avatar>
                    )
                }
                title={submission.name}
                subheader={createdAt}
            />
            <CardMedia
                className={classes.media}
                image={submission.files.preview?.presignedUrl}
                title={submission.name}
            />

            <CardContent>
                <LinearProgress
                    className={classes.progressBar}
                    variant="determinate"
                    value={submission.progress.total * 100}
                />
                <Typography
                    className={classes.progressText}
                    variant="body2"
                    color="textSecondary"
                    component="p">
                    {`Progress: ${(submission.progress.total * 100).toFixed(
                        0,
                    )}%`}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    {`Distance: ${submission.distance.toFixed(1)} miles`}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    {`Quantity Requested: ${submission.quantity}`}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    {`3D Printing Jobs Remaining: ${submission.progress.modelQuantity}`}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    {`Laser Cutting Jobs Remaining: ${submission.progress.laserCuttingQuantity}`}
                </Typography>
                {submission.description && (
                    <Typography
                        variant="body2"
                        color="textSecondary"
                        component="p">
                        {submission.description}
                    </Typography>
                )}
            </CardContent>
        </CardItem>
    )
}

export interface SubmitterSubmissionsProps {}
export const SubmitterSubmissions: React.FC<SubmitterSubmissionsProps> = () => {
    const {data, loading} = useSubmittedSubmissionsQuery()
    const submissions = useSortedSubmissions(data?.submittedSubmissions)

    if (loading) {
        return <div>Loading...</div>
    }
    assert(submissions, `Couldn't get submitted submissions!`)

    return (
        <>
            <PageTitleBar
                title="My Submissions"
                rightItem={
                    <div style={{display: "flex", flexDirection: "row"}}>
                        <SortingProperties />
                        <Button
                            color="primary"
                            onClick={() => {
                                goTo("/submission/create")
                            }}
                            style={{marginBottom: 10, marginTop: 10}}
                            variant="contained">
                            Create Submission
                        </Button>
                    </div>
                }
            />

            <GridView>
                {submissions.map((submission) => (
                    <SubmissionCard key={submission.id} {...{submission}} />
                ))}
            </GridView>
        </>
    )
}

export interface VolunteerSubmissionsProps {}
export const VolunteerSubmissions: React.FC<VolunteerSubmissionsProps> = () => {
    const {data, loading} = useAvailableSubmissionsQuery()
    const submissions = useSortedSubmissions(data?.availableSubmissions)

    if (loading) {
        return <div>Loading...</div>
    }
    assert(submissions, `Couldn't get available submissions!`)

    return (
        <>
            <PageTitleBar title="Find Jobs" rightItem={<SortingProperties />} />

            <GridView>
                {submissions.map((submission) => (
                    <SubmissionCard key={submission.id} {...{submission}} />
                ))}
            </GridView>
        </>
    )
}

const fixSortOrder = (value: number, sortOrder: SortOrder) =>
    sortOrder === "ascending" ? value : -value

const useSortedSubmissions = (submissions: Submission[] | undefined) => {
    const {sortBy, sortOrder} = useContainer(SubmissionSortContainer)
    return useMemo(() => {
        if (!submissions) {
            return undefined
        }

        switch (sortBy) {
            case "distance":
                return [...submissions].sort((a, b) =>
                    fixSortOrder(a.distance - b.distance, sortOrder),
                )

            case "date":
                return [...submissions].sort((a, b) =>
                    fixSortOrder(
                        new Date(a.createdAt).getTime() -
                            new Date(b.createdAt).getTime(),
                        sortOrder,
                    ),
                )

            case "printing":
                return [...submissions].sort((a, b) => {
                    return fixSortOrder(
                        a.progress.modelQuantity - b.progress.modelQuantity,
                        sortOrder,
                    )
                })

            case "laserCutting":
                return [...submissions].sort((a, b) =>
                    fixSortOrder(
                        a.progress.laserCuttingQuantity -
                            b.progress.laserCuttingQuantity,
                        sortOrder,
                    ),
                )

            default:
                unreachable(`Unrecognized sorting order: ${sortBy}`)
        }
    }, [sortBy, sortOrder, submissions])
}

type SortBy = "distance" | "date" | "printing" | "laserCutting"
type SortOrder = "ascending" | "descending"
export const SubmissionSortContainer = createContainer(() => {
    const [sortBy, setSortBy] = useState<SortBy>("distance")
    const [sortOrder, setSortOrder] = useState<SortOrder>("ascending")
    return {setSortBy, setSortOrder, sortBy, sortOrder}
})

export interface SortingPropertiesProps {}
export const SortingProperties: React.FC<SortingPropertiesProps> = () => {
    const {setSortBy, setSortOrder, sortBy, sortOrder} = useContainer(
        SubmissionSortContainer,
    )
    const classes = useStyles()

    return (
        <div className={classes.sortBy}>
            <FormControl>
                <InputLabel>Sort By</InputLabel>
                <Select
                    value={sortBy}
                    onChange={(event) =>
                        setSortBy((event.target.value as unknown) as SortBy)
                    }>
                    <MenuItem value="distance">Distance</MenuItem>
                    <MenuItem value="date">Date</MenuItem>
                    <MenuItem value="printing">3D Printing</MenuItem>
                    <MenuItem value="laserCutting">Laser Cutting</MenuItem>
                </Select>
            </FormControl>
            <SwitchButton
                initial={sortOrder === "ascending"}
                onStateChange={(state) => {
                    setSortOrder(state ? "ascending" : "descending")
                }}
                onIcon={<ArrowUpwardIcon />}
                offIcon={<ArrowDownwardIcon />}
            />
        </div>
    )
}

export interface SubmissionsPageProps {}
export const SubmissionsPage: React.FC<SubmissionsPageProps> = () => (
    <SubmissionSortContainer.Provider>
        <div>
            <PageComponent
                disableHide={false}
                showFooter
                title="Home"
                rolePages={[
                    {
                        component: () => <SubmitterSubmissions />,
                        role: "Submitter",
                    },
                    {
                        component: () => <VolunteerSubmissions />,
                        role: "Volunteer",
                    },
                ]}>
                <WrongPageComponent text="Must be signed in to view submissions" />
            </PageComponent>
            <br />
        </div>
    </SubmissionSortContainer.Provider>
)
