import {Logging} from "@nimashoghi/logging"
import {
    InMemoryCache,
    IntrospectionFragmentMatcher,
} from "apollo-cache-inmemory"
import {ApolloClient} from "apollo-client"
import {ApolloLink} from "apollo-link"
import {setContext} from "apollo-link-context"
import {onError} from "apollo-link-error"
import {createUploadLink} from "apollo-upload-client"
import introspectionQueryResultData from "./graphql/fragmentMatcher.generated"
import {AuthenticationResult} from "./graphql/schema.generated"
import {goTo} from "./services/utils"
import {cachedStorage, getJwtToken} from "./util"

const logger = Logging.logger({__filename})

export const login = ({
    id,
    refreshToken,
    token,
}: Omit<AuthenticationResult, "user">) => {
    cachedStorage.setItem("JWT_TOKEN", token)
    cachedStorage.setItem("REFRESH_TOKEN", refreshToken)
    cachedStorage.setItem("USER_ID", id)
}

export const logout = () => {
    cachedStorage.removeItem("JWT_TOKEN")
    cachedStorage.removeItem("REFRESH_TOKEN")
    cachedStorage.removeItem("USER_ID")
}

export type NotifyMessage = {
    message: string
    severity?: "error" | "success" | "info" | "warning"
}

const cache = new InMemoryCache({
    fragmentMatcher: new IntrospectionFragmentMatcher({
        introspectionQueryResultData,
    }),
})

const errorLink = onError(({graphQLErrors, networkError}) => {
    if (graphQLErrors) {
        graphQLErrors.forEach(({locations, message, path}) => {
            if (message === "Your user was not found in the database!") {
                goTo("/account/logout")
            }
            logger.error`[GraphQL error]: Message: ${message}, Location: ${JSON.stringify(
                locations,
            )}, Path: ${path}`
        })
    }
    if (networkError) {
        logger.error`[Network error]: ${networkError}`
    }
})

const authLink = setContext(async (_, {headers}) => {
    const token = await getJwtToken(client)

    return {
        headers: {
            ...headers,
            ...(token ? {authorization: `Bearer ${token}`} : {}),
        },
    }
})

const uploadLink = createUploadLink({
    uri:
        process.env.NODE_ENV !== "production"
            ? "http://localhost:80/graphql"
            : "https://api.operationdynamo.org",
})

const link = ApolloLink.from([errorLink, authLink, uploadLink])

export const client = new ApolloClient({cache, link})
