import React, {useCallback, useEffect} from "react"
import {LocationFeature, searchLocations} from "../../services/location-search"
import {ApiAutocomplete} from "../api-autocomplete"
import {LocationFormItem} from "./form"

export type FormLocationItemProps = LocationFormItem & {
    handleChange: (id: string, value: string | number) => void
}
export const FormLocationItem = ({
    autoComplete,
    defaultValue,
    freeSolo = false,
    handleChange,
    id,
    label,
    locationIds,
    required,
}: FormLocationItemProps) => {
    const setLocation = useCallback(
        (location: LocationFeature | undefined | null) => {
            if (location) {
                handleChange(id, location.properties.display_name)
                if (locationIds) {
                    const {latitude, longitude} = locationIds
                    if (location.geometry) {
                        handleChange(latitude, location.geometry.coordinates[1])
                        handleChange(
                            longitude,
                            location.geometry.coordinates[0],
                        )
                    }
                }
            }
        },
        [handleChange, id, locationIds],
    )

    useEffect(() => {
        setLocation(defaultValue)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <ApiAutocomplete
            textProps={{
                autoComplete,
                fullWidth: true,
                id,
                label,
                name: id,
                required,
                variant: "outlined",
            }}
            onQuery={async (query) => {
                const search = await searchLocations(query)
                return search.features
            }}
            objectResolver={(feature: LocationFeature) => {
                return feature.properties.display_name
            }}
            onTextChange={(query: string) => {
                setLocation({
                    properties: {
                        display_name: query,
                    },
                })
            }}
            freeSolo={freeSolo}
            defaultValue={defaultValue}
            onSelect={setLocation}
        />
    )
}
