import React, {useEffect, useState} from "react"
import STLViewer from "stl-viewer"
import {createContainer, useContainer} from "unstated-next"
import {dxfToImageBuffer} from "../services/thumbnail"
import {contentFromFile, setNested, svgToPng} from "../services/utils"
import {DXFViewer} from "./dxf-viewer"
import {theme} from "./theme"

export type RenderType = "stl" | "dxf" | "image"

export const FileRenderStateContainer = createContainer(() => {
    const [previewImage, changePreviewImage] = useState<{
        [id: string]:
            | {
                  [fileName: string]: string | undefined
              }
            | undefined
    }>({})
    return {
        changePreviewImage,
        previewImage,
    } as const
})

export interface FileRenderProps {
    color?: string
    height?: number
    id?: string
    name?: string
    source: {type: "url"; url: string} | {file: File; type: "file"}
    type: RenderType
    width?: number
}
export const FileRender: React.FC<FileRenderProps> = ({
    color,
    height = 200,
    id = "default",
    name = "default",
    source,
    type,
    width = 200,
}) => {
    const {changePreviewImage} = useContainer(FileRenderStateContainer)

    const [element, changeElement] = useState<JSX.Element>()
    useEffect(() => {
        const getElement = async () => {
            const inputSource = source.type === "url" ? source.url : source.file
            if (source.type === "url") {
                changePreviewImage((old) => {
                    return setNested(old, `${id}.${name}`, source.url) as never
                })
            }

            if (source.type === "file" && type === "dxf") {
                contentFromFile({
                    file: source.file,
                    onLoad: (dxf: string) => {
                        const buffer = dxfToImageBuffer({dxf})
                        const base64 = buffer.toString("base64")

                        svgToPng({
                            dataUrl: `data:image/svg+xml;base64,${base64}`,
                            onLoad: (pngUrl?: string) => {
                                changePreviewImage((old) => {
                                    return setNested(
                                        old,
                                        `${id}.${name}`,
                                        pngUrl,
                                    ) as never
                                })
                            },
                        })
                    },
                })
            }

            const element = await (async () => {
                switch (type) {
                    case "dxf":
                        return (
                            <DXFViewer
                                width={width}
                                height={height}
                                source={
                                    typeof inputSource === "string"
                                        ? {type: "url", url: inputSource}
                                        : {
                                              file: inputSource,
                                              type: "file",
                                          }
                                }
                                strokeColor={
                                    color || theme.palette.primary.light
                                }
                                strokeWidth="0.5%"
                            />
                        )
                    case "stl":
                        return (
                            <STLViewer
                                model={
                                    typeof inputSource === "string"
                                        ? inputSource
                                        : await inputSource.arrayBuffer()
                                }
                                // eslint-disable-next-line @typescript-eslint/no-explicit-any
                                getPainter={(painter: any) => {
                                    setTimeout(() => {
                                        changePreviewImage((old) => {
                                            return setNested(
                                                old,
                                                `${id}.${name}`,
                                                painter.captureFrame(),
                                            ) as never
                                        })
                                    }, 500)
                                }}
                                width={width}
                                height={height}
                                modelColor={
                                    color || theme.palette.primary.light
                                }
                                backgroundColor="white"
                                rotate
                                orbitControls
                            />
                        )
                    default:
                        return (
                            <img
                                width={width}
                                height={height}
                                alt=""
                                src={
                                    typeof inputSource === "string"
                                        ? inputSource
                                        : undefined
                                }
                            />
                        )
                }
            })()

            changeElement(element)
        }
        getElement()

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return element || null
}
