import {
    ButtonBase,
    Card,
    createStyles,
    Grow,
    makeStyles,
} from "@material-ui/core"
import React from "react"

const useStyles = makeStyles(() =>
    createStyles({
        card: {
            margin: 20,
            textDecoration: "none",
            width: 345,
        },

        details: {
            display: "flex",
            flexDirection: "column",
        },
    }),
)

export interface CardItemProps {
    children?: JSX.Element | JSX.Element[] | null
    onClick?: () => void
}
export const CardItem: React.FC<CardItemProps> = ({children, onClick}) => {
    const classes = useStyles()
    return (
        <div>
            <Grow in timeout={500}>
                <ButtonBase>
                    <Card onClick={onClick} className={classes.card}>
                        <div className={classes.details}>
                            {children || null}
                        </div>
                    </Card>
                </ButtonBase>
            </Grow>
        </div>
    )
}
