/* eslint-disable @typescript-eslint/no-explicit-any */
import {removeIn} from "./utils"

export const jsonToCsv = (
    objects: {[id: string]: any}[],
    objectHandler?: (obj: {[id: string]: any}) => string,
) => {
    if (objects.length >= 1) {
        const title = Object.keys(
            removeIn(removeIn(objects[0], "__typename"), "file"),
        ).join(",")
        const values = objects
            .map((object) =>
                Object.values(removeIn(removeIn(object, "__typename"), "file")),
            )
            .map((values) =>
                values
                    .map((value) => {
                        if (value) {
                            switch (typeof value) {
                                case "object":
                                    if (objectHandler) {
                                        return objectHandler(value)
                                    }
                                    return ""

                                default:
                                    return `"${String(value).trim()}"`
                            }
                        }
                        return ""
                    })
                    .join(","),
            )
            .join("\n")
        return `${title}\n${values}`
    }
    throw new Error("Must be greater than 0 elements in objects")
}
