import {Typography} from "@material-ui/core"
import Avatar from "@material-ui/core/Avatar"
import Container from "@material-ui/core/Container"
import Grid from "@material-ui/core/Grid"
import Link from "@material-ui/core/Link"
import {makeStyles} from "@material-ui/core/styles"
import LockOutlinedIcon from "@material-ui/icons/LockOutlined"
import React, {useCallback} from "react"
import {login} from "../../client"
import {FormComponent, FormItem} from "../../components/form/form"
import {PageComponent} from "../../components/page"
import {
    MeDocument,
    MutationLoginArgs,
    useLoginMutation,
} from "../../graphql/schema.generated"
import {useSnack} from "../../hooks"
import {goTo} from "../../services/utils"

const useStyles = makeStyles((theme) => ({
    avatar: {
        backgroundColor: theme.palette.secondary.main,
        margin: theme.spacing(1),
    },
    paper: {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        marginTop: theme.spacing(8),
    },
}))

const formItems = [
    {
        alignment: {xs: 12},
        autoComplete: "email",
        id: ".email",
        label: "Email",
        required: true,
        type: "text",
    },
    {
        alignment: {xs: 12},
        autoComplete: "password",
        id: ".password",
        label: "Password",
        required: true,
        textType: "password",
        type: "text",
    },
] as FormItem[]

export interface LoginPageViewProps {
    onSubmit: (form: MutationLoginArgs) => void
}
export const LoginPageView: React.FC<LoginPageViewProps> = ({onSubmit}) => {
    const classes = useStyles()

    return (
        <Container component="main" maxWidth="xs">
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Login
                </Typography>

                <FormComponent
                    submitButtonTitle="Login"
                    formItems={formItems}
                    onSubmit={onSubmit}>
                    <Grid style={{marginTop: 10}} container justify="flex-end">
                        <Grid item>
                            <Link href="/account/register" variant="body2">
                                Don&apos;t have an account?
                            </Link>
                        </Grid>
                    </Grid>

                    <Grid container justify="flex-end">
                        <Grid item>
                            <Link
                                href="/account/forgot-password"
                                variant="body2">
                                Forgot Password?
                            </Link>
                        </Grid>
                    </Grid>
                </FormComponent>
            </div>
        </Container>
    )
}

export interface LoginPageProps {}
export const LoginPage: React.FC<LoginPageProps> = () => {
    const {updateSnack} = useSnack()
    const [mutate] = useLoginMutation({
        onCompleted: (data) => {
            if (data.login) {
                login(data.login)
                goTo("/")
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
        refetchQueries: [{query: MeDocument}],
    })

    const onSubmit = useCallback(
        (args: MutationLoginArgs) => {
            mutate({
                variables: args,
            })
        },
        [mutate],
    )

    return (
        <PageComponent
            showFooter
            title="Login"
            rolePages={[
                {redirect: "/", role: "Submitter"},
                {redirect: "/", role: "Volunteer"},
            ]}>
            <LoginPageView onSubmit={onSubmit} />
        </PageComponent>
    )
}
