import {makeStyles, Slider, Typography} from "@material-ui/core"
import React, {useEffect, useMemo} from "react"
import {getNested} from "../../services/utils"
import {SliderFormItem} from "./form"

const useStyles = makeStyles({
    root: {
        flex: 1,
    },
})

const valuetext = (value: number, ending: string) => `${value} ${ending}`

export type FormSliderItemProps = SliderFormItem & {
    form: any
    handleChange: (id: string, value: number) => void
}
export const FormSliderItem = ({
    defaultValue = 0,
    ending = "",
    form,
    handleChange,
    id,
    label,
    max,
    min,
    step,
}: FormSliderItemProps) => {
    const classes = useStyles()
    useEffect(() => {
        handleChange(id, defaultValue)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    const value = useMemo(() => getNested(form, id), [form, id])

    return (
        <div className={classes.root}>
            <Typography id="discrete-slider" gutterBottom>
                {`${label} (${value})`}
            </Typography>
            <Slider
                id={id}
                onChange={(_, value) => {
                    if (typeof value === "number") {
                        handleChange(id, value)
                    }
                }}
                defaultValue={defaultValue}
                getAriaValueText={(value) => valuetext(value, ending)}
                aria-labelledby="discrete-slider-always"
                step={step}
                valueLabelDisplay="auto"
                min={min}
                max={max}
            />
        </div>
    )
}
