import {assert} from "@dynamo/common"
import {Logging} from "@nimashoghi/logging"
import {RenderType} from "../components/file-renderer"
import {JobType} from "../graphql/schema.generated"

const logger = Logging.logger({__filename})

export const nestedIndex = (path: string) => {
    const indices = path.split(".").slice(1)
    const beforeIndex: string | undefined = indices[indices.length - 2]
    const lastIndex = indices[indices.length - 1]
    return {beforeIndex, indices, lastIndex}
}

export const removeIn = <T>(object: {[key: string]: T}, key: string) => {
    const copy = {...object}
    delete copy[key]
    return copy
}

export const capitalize = (string: string, all = false) => {
    const regex = all ? /(?:^|\s)\S/g : /(?:^|\s)\S/
    return string.replace(regex, (a) => {
        return a.toUpperCase()
    })
}

export const contentFromFile = ({
    file,
    onLoad,
}: {
    file: File
    onLoad: (content: string) => void
}) => {
    const reader = new FileReader()
    reader.onerror = () => {
        logger.error`error reading file`
    }
    reader.onload = (event) => {
        onLoad(event.target?.result as string)
    }
    reader.readAsText(file)
}

export const svgToPng = ({
    dataUrl,
    onLoad,
}: {
    dataUrl: string
    onLoad: (pngUrl?: string) => void
}) => {
    const canvas = document.createElement("canvas")
    const context = canvas?.getContext("2d")

    const image = new Image()
    image.src = dataUrl

    image.onload = () => {
        context?.drawImage(image, 75, 0)
        const pngUrl = canvas?.toDataURL("image/png")
        onLoad(pngUrl)
    }
}

export const dataURLtoFile = (dataUrl: string, filename: string) => {
    try {
        const arr = dataUrl.split(",")
        const first = arr[0]
        assert(!!first[0], "invalid data url")
        const match = first.match(/:(.*?);/)
        assert(!!match, "invalid data url")
        const mime = match[1]
        const bstr = atob(arr[1])
        let n = bstr.length
        const u8arr = new Uint8Array(n)

        while (n--) {
            u8arr[n] = bstr.charCodeAt(n)
        }

        return new File([u8arr], filename, {type: mime})
    } catch {
        return undefined
    }
}

export const urlFromFile = ({
    file,
    onLoad,
}: {
    file: File
    onLoad: (url: string) => void
}) => {
    const reader = new FileReader()
    reader.onerror = () => {
        logger.error`error reading file`
    }
    reader.onload = (event) => {
        onLoad(event.target?.result as string)
    }
    reader.readAsDataURL(file)
}

export const goToNewTab = (url: string) => {
    window.open(url, "_blank")
}

export const goTo = (url: string) => {
    window.open(url, "_self")
}

const nested = <T>(object: {[key: string]: T}, path: string) => {
    const root = {...object}
    const {beforeIndex, indices, lastIndex} = nestedIndex(path)
    return {
        beforeIndex,
        lastIndex,
        previous: indices.slice(0, -1).reduce(
            // eslint-disable-next-line no-return-assign
            (acc, current) =>
                acc[current] !== undefined
                    ? acc[current]
                    : // eslint-disable-next-line @typescript-eslint/no-explicit-any, no-param-reassign
                      (acc[current] = {} as any),
            root,
        ),
        root,
    }
}

export const getRenderType = (jobType: JobType): RenderType => {
    switch (jobType) {
        case "LaserCutting":
            return "dxf"
        case "Printing":
            return "stl"
    }
}

export const renderFileType = (name: string) => {
    const type = name.match(/(.*)\.(.*)/)?.[2]?.toLowerCase()
    if (
        type === "png" ||
        type === "png" ||
        type === "jpeg" ||
        type === "jpg" ||
        type === "tif" ||
        type === "svg" ||
        type === "gif"
    ) {
        return "image"
    }
    return type as RenderType | undefined
}

export const partition = <T>(array: T[], isValid: (element: T) => boolean) => {
    return array.reduce<T[][]>(
        ([p, f], e) => (isValid(e) ? [[...p, e], f] : [p, [...f, e]]),
        [[], []],
    )
}

export const setNested = <T>(object: {}, path: string, value: T) => {
    const {lastIndex, previous, root} = nested(object, path)
    previous[lastIndex] = value
    return root
}

export const removeNested = <T>(
    object: {[key: string]: T},
    path: string,
): {[key: string]: T} => {
    const {beforeIndex, lastIndex, previous, root} = nested(object, path)
    delete previous[lastIndex]

    if (Object.keys(previous).length === 0) {
        if (beforeIndex) {
            return removeNested<T>(root, `.${beforeIndex}`)
        }
    }
    return root
}

export const getNested = <T, C>(
    object: Partial<T>,
    path: string,
): C | undefined => {
    const {lastIndex, previous} = nested(object, path)
    return previous[lastIndex] as C | undefined
}

/**
 * Determine the mobile operating system.
 * This function returns one of 'iOS', 'Android', 'Windows Phone', or 'unknown'.
 *
 * @returns {String}
 */
export const getMobileOperatingSystem = (): "android" | "ios" | "unknown" => {
    const userAgent =
        navigator.userAgent ||
        navigator.vendor ||
        ((window as unknown) as {opera: string}).opera

    if (/android/i.test(userAgent)) {
        return "android"
    }

    // iOS detection from: http://stackoverflow.com/a/9039885/177710
    if (/iPad|iPhone|iPod/.test(userAgent)) {
        return "ios"
    }

    return "unknown"
}
