import {InputLabel, MenuItem, Select} from "@material-ui/core"
import FormControl from "@material-ui/core/FormControl"
import HelpIcon from "@material-ui/icons/Help"
import React, {useEffect, useState} from "react"
import {TooltipComponent} from "../tooltip"
import {SelectFormItem} from "./form"

export type FormSelectItemProps = SelectFormItem & {
    handleChange: (id: string, value: string) => void
}
export const FormSelectItem: React.FC<FormSelectItemProps> = ({
    autoComplete,
    defaultValue = "",
    disabled = false,
    handleChange,
    hint,
    id,
    label,
    options,
    required,
}) => {
    const [selectValue, changeSelectValue] = useState<string>(defaultValue)

    useEffect(() => {
        handleChange(id, defaultValue)
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <FormControl required={required} variant="outlined" fullWidth>
            <InputLabel id="select-outlined-label">{label}</InputLabel>
            <Select
                disabled={disabled}
                endAdornment={
                    hint ? (
                        <TooltipComponent hint={hint} icon={<HelpIcon />} />
                    ) : undefined
                }
                required={required}
                value={selectValue}
                autoComplete={autoComplete}
                labelId="select-outlined-label"
                id={id}
                onChange={({target: {value}}) => {
                    if (typeof value === "string") {
                        changeSelectValue(value)
                        handleChange(id, value)
                    }
                }}
                label={label}>
                {Object.entries(options).map(([key, value]) => (
                    <MenuItem {...{key}} value={key}>
                        {value}
                    </MenuItem>
                ))}
            </Select>
        </FormControl>
    )
}
