import {Snackbar} from "@material-ui/core"
import MuiAlert from "@material-ui/lab/Alert"
import React from "react"
import {NotifyMessage} from "../client"

export interface SnackBarComponentProps {
    state: [
        NotifyMessage | undefined,
        React.Dispatch<React.SetStateAction<NotifyMessage | undefined>>,
    ]
}
export const SnackBarComponent: React.FC<SnackBarComponentProps> = ({
    state: [snack, updateSnack],
}) => {
    return (
        <Snackbar
            open={!!snack}
            autoHideDuration={6000}
            onClose={(event) => {
                if (!event) {
                    updateSnack(undefined)
                }
            }}>
            <MuiAlert
                elevation={6}
                variant="filled"
                severity={snack?.severity || "error"}>
                {snack ? snack.message.replace("GraphQL error:", "") : ""}
            </MuiAlert>
        </Snackbar>
    )
}
