import React from "react"
import {PageComponent} from "../components/page"
import {WrongPageComponent} from "../components/wrong-page"
import {SubmissionSortContainer, VolunteerSubmissions} from "./submissions"

export interface FindPageViewProps {}
export const FindPageView: React.FC<FindPageViewProps> = () => {
    return (
        <div style={{padding: 10}}>
            <VolunteerSubmissions />
        </div>
    )
}

export interface FindPageProps {}
export const FindPage: React.FC<FindPageProps> = () => {
    return (
        <SubmissionSortContainer.Provider>
            <div>
                <PageComponent
                    disableHide={false}
                    showFooter
                    title="Home"
                    rolePages={[
                        {
                            component: () => <FindPageView />,
                            role: "Volunteer",
                        },
                    ]}>
                    <WrongPageComponent text="Must be signed in as Volunteer to view submissions" />
                </PageComponent>
                <br />
            </div>
        </SubmissionSortContainer.Provider>
    )
}
