/* eslint-disable @typescript-eslint/no-explicit-any */

import {useState} from "react"
import {setNested} from "../services/utils"

export const useForm = <
    T extends {
        [key: string]: any
    }
>(
    initial: Partial<T> | undefined = {},
) => {
    const [form, changeForm] = useState<Partial<T>>(initial)

    const updateForm = (path: string, value: any) => {
        changeForm((old) => {
            const newV: Partial<T> = setNested(old, path, value) as any
            return newV
        })
    }
    return {form, updateForm}
}
