import {Typography} from "@material-ui/core"
import BackupIcon from "@material-ui/icons/Backup"
import React, {useCallback, useMemo} from "react"
import Dropzone, {IFileWithMeta, StatusValue} from "react-dropzone-uploader"
import "react-dropzone-uploader/dist/styles.css"
import {useSnack} from "../../hooks"
import {theme} from "../theme"
import "./extra.css"
import {FileUploadFormItem} from "./form"

const MAX_SIZE = 30 ** 20

export type FormFileUploadItemProps = FileUploadFormItem & {
    handleChange: (id: string, value: File[] | File | undefined) => void
}
export const FormFileUploadItem: React.FC<FormFileUploadItemProps> = ({
    PreviewComponent,
    accept,
    handleChange,
    icon,
    id,
    label,
    multiple = true,
    defaultValue = [],
}) => {
    const {updateSnack} = useSnack()

    const handleChangeStatus = useCallback(
        (_: IFileWithMeta, status: StatusValue) => {
            if (status.includes("rejected")) {
                updateSnack({message: status})
            }
        },
        [updateSnack],
    )

    const fileUploadComponent = useMemo(() => {
        return (
            <Dropzone
                PreviewComponent={
                    PreviewComponent &&
                    ((props) => (
                        // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
                        // @ts-ignore
                        <PreviewComponent
                            {...props}
                            id={id}
                            handleChange={(
                                value: File[] | File | undefined,
                            ) => {
                                handleChange(id, value)
                            }}
                        />
                    ))
                }
                SubmitButtonComponent={null as never}
                maxSizeBytes={MAX_SIZE}
                initialFiles={
                    Array.isArray(defaultValue) ? defaultValue : [defaultValue]
                }
                onChangeStatus={handleChangeStatus}
                accept={accept}
                autoUpload
                maxFiles={multiple ? 10 : 1}
                multiple={multiple}
                inputContent={(_, extra) => {
                    return extra.reject ? (
                        "File type not supported"
                    ) : (
                        <div
                            key="upload-btn"
                            style={{display: "flex", flexDirection: "row"}}>
                            {icon || <BackupIcon />}
                            <Typography
                                style={{marginLeft: 10}}
                                variant="body1">
                                {label.toUpperCase()}
                            </Typography>
                        </div>
                    )
                }}
                styles={{
                    dropzone: {
                        borderColor: theme.palette.primary.main,
                        padding: 10,
                    },

                    dropzoneActive: {
                        backgroundColor: "rgba(46,73,168,0.1)",
                    },
                    dropzoneReject: {
                        backgroundColor: "#DAA",
                        borderColor: "red",
                    },
                    inputLabel: (_, extra) =>
                        extra.reject
                            ? {color: "red"}
                            : {color: theme.palette.primary.main},
                }}
            />
        )
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return fileUploadComponent
}
