/* eslint-disable no-use-before-define */

import TextField, {TextFieldProps} from "@material-ui/core/TextField"
import Autocomplete from "@material-ui/lab/Autocomplete"
import React, {useEffect, useState} from "react"
import {useDebounce} from "../hooks/useDebounce"

export interface ApiAutocompleteProps<T> {
    defaultValue?: T
    freeSolo: boolean
    objectResolver: (object: T) => string
    onQuery: (query: string) => Promise<T[]>
    onSelect?: (object: T | null) => void
    onTextChange?: (value: string) => void
    textProps?: TextFieldProps
}
export function ApiAutocomplete<T>({
    defaultValue,
    freeSolo,
    objectResolver,
    onQuery,
    onSelect,
    onTextChange,
    textProps,
}: ApiAutocompleteProps<T>) {
    const [text, changeText] = useState<string>("")
    const debouncedText = useDebounce(text, 500)
    const [options, changeOptions] = useState<T[]>([])
    useEffect(() => {
        const search = async () => {
            if (debouncedText !== "") {
                const search = await onQuery(debouncedText)
                changeOptions(() => {
                    return [...search]
                })
            }
        }
        search()
    }, [debouncedText, onQuery])

    return (
        <Autocomplete
            defaultValue={defaultValue}
            freeSolo={freeSolo}
            id="combo-box-demo"
            filterOptions={(x) => x}
            onChange={(_: unknown, newValue: T | null) => {
                if (onSelect) {
                    onSelect(newValue)
                }
            }}
            renderInput={(params) => (
                <TextField
                    {...params}
                    {...textProps}
                    onChange={({target: {value}}) => {
                        changeText(value)
                        if (onSelect && onTextChange) {
                            onTextChange(value)
                        }
                    }}
                />
            )}
            options={options}
            getOptionLabel={(option) => objectResolver(option)}
        />
    )
}
