import {assert} from "@dynamo/common"
import {
    Button,
    Card,
    CardContent,
    createStyles,
    FormControl,
    InputLabel,
    Link,
    makeStyles,
    MenuItem,
    Select,
    Theme,
    Typography,
} from "@material-ui/core"
import OutlinedInput from "@material-ui/core/OutlinedInput"
import AssignmentIcon from "@material-ui/icons/Assignment"
import DeleteIcon from "@material-ui/icons/Delete"
import DoneAllIcon from "@material-ui/icons/DoneAll"
import HourglassEmptyIcon from "@material-ui/icons/HourglassEmpty"
import * as React from "react"
import {useCallback, useState} from "react"
import {FileItemPreview} from "../components/file-item-preview"
import {InfoListComponent, InfoListItem} from "../components/info-list"
import {PageComponent, RolePage} from "../components/page"
import {CustomizedSteppers, StepperStep} from "../components/stepper"
import {WrongPageComponent} from "../components/wrong-page"
import {
    Job,
    JobProgress,
    useCancelJobMutation,
    useJobQuery,
    UserType,
    useUpdateJobProgressMutation,
} from "../graphql/schema.generated"
import {useDateFromNow, useQuery, useSnack} from "../hooks"
import {getRenderType, goTo} from "../services/utils"

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        button: {
            margin: theme.spacing(1),
        },

        jobPageView: {
            maxWidth: 1200,
            minWidth: 500,
            width: "50vw",
        },

        viewOriginalSubmissionButton: {marginBottom: 35},
    }),
)

const shippingProviders = ["UPS", "USPS", "Fedex", "Other"] as const
type ShippingType = typeof shippingProviders[number]

enum ProgressStep {
    Assigned = 0,
    PrintStarted = 1,
    PrintComplete = 2,
    Shipped = 3,
    Received = 4,
}
const jobStepperSteps: StepperStep[] = [
    {icon: <AssignmentIcon />, label: "Assigned"},
    {icon: <HourglassEmptyIcon />, label: "Print Started"},
    {icon: <HourglassEmptyIcon />, label: "Print Complete"},
    {icon: <HourglassEmptyIcon />, label: "Shipped"},
    {icon: <DoneAllIcon />, label: "Received"},
]

export interface PrintProps {
    onClickPrint?: () => void
    updateTrackingNumber: (trackingNumber?: string) => void
}
export const PrintCompleteView: React.FC<PrintProps> = ({
    onClickPrint = print,
    updateTrackingNumber,
}) => {
    const [shippingProvider, changeShippingProvider] = useState<ShippingType>(
        "UPS",
    )

    return (
        <div>
            <Link {...{onClick: onClickPrint}}>
                <Typography
                    style={{
                        alignSelf: "center",
                        marginBottom: 10,
                    }}
                    variant="subtitle1">
                    Print this page and send in package
                </Typography>
            </Link>

            <FormControl variant="outlined">
                <InputLabel id="shipping-select-label">Type</InputLabel>
                <Select
                    labelId="shipping-select-label"
                    label="Type"
                    id="shipping-select"
                    value={shippingProvider}
                    onChange={({target: {value}}) => {
                        if (typeof value === "string") {
                            changeShippingProvider(value as ShippingType)
                        }
                    }}>
                    {shippingProviders.map((shippingProvider, i) => {
                        return (
                            // eslint-disable-next-line react/no-array-index-key
                            <MenuItem key={i} value={shippingProvider}>
                                {shippingProvider}
                            </MenuItem>
                        )
                    })}
                </Select>
            </FormControl>

            <FormControl style={{marginLeft: 10}} variant="outlined">
                <InputLabel htmlFor="tracking-number">
                    Tracking Number
                </InputLabel>
                <OutlinedInput
                    label="Tracking Number"
                    id="tracking-number"
                    onChange={({target: {value}}) => {
                        updateTrackingNumber(
                            value
                                ? `${shippingProvider}-${value.trim()}`
                                : undefined,
                        )
                    }}
                />
            </FormControl>
        </div>
    )
}

export interface JobStatusPageProps extends JobPageViewProps {}
export const JobPageStatus: React.FC<JobStatusPageProps> = ({
    cancelJob,
    job: {id, progress},
    showActionButtons,
    updateJobProgress,
}) => {
    const classes = useStyles()
    const {updateSnack} = useSnack()
    const [trackingNumber, changeTrackingNumber] = useState<string>()
    const {done, step} = {
        done: progress === "Shipped" || progress === "Received",
        step: ProgressStep[progress],
    }
    const nextStep = step + 1

    return (
        <div>
            <CustomizedSteppers currentStep={step} steps={jobStepperSteps} />
            <div>
                {progress === "PrintComplete" && (
                    <PrintCompleteView
                        updateTrackingNumber={changeTrackingNumber}
                    />
                )}

                {!done && showActionButtons && (
                    <div>
                        <Button
                            variant="contained"
                            color="secondary"
                            onClick={() => {
                                cancelJob(id)
                            }}
                            className={classes.button}
                            startIcon={<DeleteIcon />}>
                            Cancel Job
                        </Button>

                        <Button
                            variant="contained"
                            color="primary"
                            disabled={done}
                            onClick={() => {
                                if (
                                    progress === "PrintComplete" &&
                                    !trackingNumber
                                ) {
                                    updateSnack({
                                        message:
                                            "Tracking number is required. For other shipping options please pick other and explain",
                                    })
                                } else {
                                    updateJobProgress({
                                        id,
                                        progress: String(
                                            ProgressStep[nextStep],
                                        ) as JobProgress,
                                        trackingNumber,
                                    })
                                }
                            }}
                            className={classes.button}
                            endIcon={jobStepperSteps[nextStep].icon}>
                            {jobStepperSteps[nextStep].label}
                        </Button>
                    </div>
                )}
            </div>
        </div>
    )
}

export interface JobPageListProps extends JobPageViewProps {}
export const JobPageList: React.FC<JobPageListProps> = ({
    job: {
        createdAt,
        description,
        id,
        quantity,
        submission: {address, submitter},
        trackingNumber,
        type,
        volunteer,
    },
}) => {
    const date = useDateFromNow(createdAt)
    return (
        <InfoListComponent
            items={
                [
                    {
                        action: {type: "copy"},
                        description: id,
                        name: "Id",
                    },
                    {
                        action: {type: "copy"},
                        description: quantity,
                        name: "Quantity",
                    },
                    {
                        action: {type: "copy"},
                        description: date,
                        name: "Created",
                    },
                    {
                        action: {type: "copy"},
                        description: description ?? "N/A",
                        name: "Description",
                    },
                    {
                        action: {type: "copy", value: submitter.email},
                        description: `${submitter.firstName} ${submitter.lastName} <${submitter.email}>`,
                        name: "Submitter",
                    },
                    {
                        action: {type: "copy", value: volunteer},
                        description: volunteer
                            ? `${volunteer.firstName} ${volunteer.lastName} <${volunteer.email}>`
                            : "Not Assigned Yet",
                        name: "Assignee",
                    },
                    {
                        action: {type: "copy"},
                        description: address,
                        name: "Shipping Address",
                    },
                    {
                        action: {type: "copy"},
                        description: trackingNumber || "N/A",
                        name: "Tracking Number",
                    },
                    {
                        action: {type: "copy"},
                        description:
                            type === "LaserCutting" ? "Laser Cut" : "3D Print",
                        name: "Job Type",
                    },
                ] as InfoListItem[]
            }
        />
    )
}

export interface JobPageViewProps {
    cancelJob: (id: string) => void
    job: Job
    showActionButtons: boolean
    updateJobProgress: (args: {
        id: string
        progress: JobProgress
        trackingNumber?: string | undefined
    }) => void
}
export const JobPageView: React.FC<JobPageViewProps> = (props) => {
    const {
        job: {createdAt, files, name, submission, type},
    } = props
    const date = useDateFromNow(createdAt)
    const classes = useStyles()

    const onSubmissionClick = useCallback(
        () => goTo(`/submission/?id=${submission.id}`),
        [submission.id],
    )

    const renderType = getRenderType(type)

    return (
        <div className={classes.jobPageView}>
            <Card>
                <CardContent style={{textAlign: "center"}}>
                    <Typography component="h5" variant="h5">
                        {name}
                    </Typography>
                    <Typography color="textSecondary" variant="h6">
                        {date}
                    </Typography>
                    <Button
                        style={{marginTop: 10}}
                        className={classes.viewOriginalSubmissionButton}
                        variant="contained"
                        color="primary"
                        onClick={onSubmissionClick}>
                        View Original Submission
                    </Button>
                    <FileItemPreview
                        items={files.map((file) => {
                            return {file, type: renderType}
                        })}
                    />

                    <JobPageList {...props} />

                    <JobPageStatus {...props} />
                </CardContent>
            </Card>
        </div>
    )
}

export interface JobPageProps {}
export const JobPage: React.FC<JobPageProps> = () => {
    const {updateSnack} = useSnack()
    const id = useQuery().get("id")

    assert(id, "Invalid job ID!")

    const {data} = useJobQuery({
        onError: ({message}) => {
            updateSnack({message})
        },
        variables: {id},
    })

    const [cancelJobMutation] = useCancelJobMutation({
        onCompleted: (cancelResponse) => {
            if (cancelResponse.cancelJob) {
                goTo("/")
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
    })

    const [updateJobProgressMutation] = useUpdateJobProgressMutation({
        onError: ({message}) => {
            updateSnack({message})
        },
    })

    const cancelJob = useCallback(
        (id: string) => {
            cancelJobMutation({variables: {id}})
        },
        [cancelJobMutation],
    )

    const updateJobProgress = useCallback(
        (args: {
            id: string
            progress: JobProgress
            trackingNumber?: string
        }) => {
            updateJobProgressMutation({variables: args})
        },
        [updateJobProgressMutation],
    )

    const jopPage = useCallback(
        (userType: UserType, showActionButtons: boolean): RolePage => {
            return {
                component: (user) => (
                    <div style={{display: "flex", justifyContent: "center"}}>
                        {data?.job && (
                            <JobPageView
                                job={data.job}
                                {...{
                                    cancelJob,
                                    showActionButtons,
                                    updateJobProgress,
                                    user,
                                }}
                            />
                        )}
                    </div>
                ),
                role: userType,
            }
        },
        [cancelJob, data, updateJobProgress],
    )

    return (
        <PageComponent
            disableHide={false}
            showFooter
            title="Job"
            rolePages={[
                jopPage("Submitter", false),
                jopPage("Volunteer", true),
            ]}>
            <WrongPageComponent text="Must be signed in to view Job" />
        </PageComponent>
    )
}
