import {Typography} from "@material-ui/core"
import Avatar from "@material-ui/core/Avatar"
import Container from "@material-ui/core/Container"
import CssBaseline from "@material-ui/core/CssBaseline"
import Grid from "@material-ui/core/Grid"
import Link from "@material-ui/core/Link"
import {makeStyles} from "@material-ui/core/styles"
import LockOutlinedIcon from "@material-ui/icons/LockOutlined"
import React, {useCallback, useMemo, useState} from "react"
import {login} from "../../client"
import {FormComponent, FormItem} from "../../components/form/form"
import {PageComponent} from "../../components/page"
import {
    MeDocument,
    MutationRegisterArgs,
    useRegisterMutation,
    UserType,
} from "../../graphql/schema.generated"
import {useQuery, useSnack} from "../../hooks"
import {goTo} from "../../services/utils"

const useStyles = makeStyles((theme) => ({
    avatar: {
        backgroundColor: theme.palette.secondary.main,
        margin: theme.spacing(1),
    },
    paper: {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        marginTop: theme.spacing(8),
    },
}))

export interface RegisterPageViewProps {
    onSubmit: (
        form: MutationRegisterArgs & {
            confirmPassword: string
        },
    ) => void
}
export const RegisterPageView: React.FC<RegisterPageViewProps> = ({
    onSubmit,
}) => {
    const classes = useStyles()
    const query = useQuery()
    const argUserType = useMemo(() => query.get("type"), [query])
    const registrationToken = query.get("token")
    const [userType, changeUserType] = useState<UserType>(
        argUserType &&
            (argUserType === "Submitter" || argUserType === "Volunteer")
            ? argUserType
            : "Volunteer",
    )
    const formItems = useMemo(
        () =>
            [
                {
                    alignment: {xs: 12},
                    defaultValue: userType,
                    id: ".type",
                    label: "User Type",
                    options: {
                        Submitter: "Medical",
                        Volunteer: "Maker",
                    },
                    required: true,
                    type: "select",
                },

                {
                    alignment: {xs: 12},
                    defaultValue: registrationToken || "",
                    hint: "generated for you by admin",
                    id: ".registrationToken",
                    label: "Registration Token",
                    required: true,
                    type: "text",
                },

                {
                    alignment: {xs: 12},
                    autoComplete: "email",
                    hint:
                        userType === "Submitter"
                            ? "this email can be viewed by makers to contact you"
                            : undefined,
                    id: ".email",
                    label:
                        userType === "Submitter"
                            ? "Email (public to volunteers)"
                            : "Email",
                    required: true,
                    type: "text",
                },
                {
                    alignment: {xs: 12},
                    autoComplete: "password",
                    id: ".password",
                    label: "Password",
                    required: true,
                    textType: "password",
                    type: "text",
                },

                {
                    alignment: {xs: 12},
                    autoComplete: "password",
                    id: ".confirmPassword",
                    label: "Confirm Password",
                    required: true,
                    textType: "password",
                    type: "text",
                },

                {
                    alignment: {sm: 6, xs: 12},
                    autoComplete: "fname",
                    id: ".firstName",
                    label: "First Name",
                    required: true,
                    type: "text",
                },

                {
                    alignment: {sm: 6, xs: 12},
                    autoComplete: "lname",
                    id: ".lastName",
                    label: "Last Name",
                    required: true,
                    type: "text",
                },

                ...(userType === "Submitter"
                    ? [
                          {
                              alignment: {xs: 12},
                              autoComplete: "address",
                              freeSolo: true,
                              hint:
                                  "address 3D printer owners should send parts",
                              id: ".address",
                              label: "Shipping Address",
                              locationIds: {
                                  latitude: ".geolocation.latitude",
                                  longitude: ".geolocation.longitude",
                              },
                              required: true,
                              type: "location",
                          },
                      ]
                    : []),

                {
                    alignment: {xs: 12},
                    autoComplete: "address-level2",
                    hint: "city your are based to find hospitals nearby",
                    id: userType === "Submitter" ? ".city" : ".address",
                    label: "City",
                    locationIds: {
                        latitude: ".geolocation.latitude",
                        longitude: ".geolocation.longitude",
                    },
                    required: true,
                    type: "location",
                },
            ] as FormItem[],
        [registrationToken, userType],
    )

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    {`${
                        userType === "Submitter" ? "Medical" : "Maker"
                    } Register`}
                </Typography>

                <FormComponent
                    submitButtonTitle="Register"
                    formItems={formItems}
                    onUpdate={(id, value) => {
                        if (id === ".type") {
                            changeUserType(value)
                        }
                    }}
                    onSubmit={onSubmit}>
                    <Grid style={{marginTop: 10}} container justify="flex-end">
                        <Grid item>
                            <Link href="/account/login" variant="body2">
                                Already have an account? Login
                            </Link>
                        </Grid>
                    </Grid>
                </FormComponent>
            </div>
        </Container>
    )
}

export interface RegisterPageProps {}
export const RegisterPage: React.FC<RegisterPageProps> = () => {
    const {updateSnack} = useSnack()
    const [mutate] = useRegisterMutation({
        onCompleted: (data) => {
            if (data.register) {
                login(data.register)
                goTo("/")
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
        refetchQueries: [{query: MeDocument}],
    })
    const onSubmit = useCallback(
        (
            args: MutationRegisterArgs & {
                confirmPassword: string
            },
        ) => {
            if (args.password === args.confirmPassword) {
                mutate({variables: {...args}})
            } else {
                updateSnack({message: "passwords do not match"})
            }
        },
        [mutate, updateSnack],
    )

    return (
        <PageComponent
            showFooter
            title="Register"
            rolePages={[
                {redirect: "/", role: "Submitter"},
                {redirect: "/", role: "Volunteer"},
            ]}>
            <RegisterPageView onSubmit={onSubmit} />
        </PageComponent>
    )
}
