import {Typography} from "@material-ui/core"
import Avatar from "@material-ui/core/Avatar"
import Container from "@material-ui/core/Container"
import {makeStyles} from "@material-ui/core/styles"
import LockOutlinedIcon from "@material-ui/icons/LockOutlined"
import React, {useCallback, useMemo} from "react"
import {FormComponent, FormItem} from "../../components/form/form"
import {PageComponent} from "../../components/page"
import {WrongPageComponent} from "../../components/wrong-page"
import {
    MutationSubmitForgotPasswordArgs,
    useSubmitForgotPasswordMutation,
} from "../../graphql/schema.generated"
import {useQuery, useSnack} from "../../hooks"
import {goTo} from "../../services/utils"

const useStyles = makeStyles((theme) => ({
    avatar: {
        backgroundColor: theme.palette.secondary.main,
        margin: theme.spacing(1),
    },
    paper: {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        marginTop: theme.spacing(8),
    },
}))

const formItems: FormItem[] = [
    {
        alignment: {xs: 12},
        autoComplete: "password",
        id: ".password",
        label: "Password",
        required: true,
        textType: "password",
        type: "text",
    },
    {
        alignment: {xs: 12},
        autoComplete: "password",
        id: ".confirmPassword",
        label: "Confirm Password",
        required: true,
        textType: "password",
        type: "text",
    },
]

export interface PasswordResetPageViewProps {
    onSubmit: (
        form: MutationSubmitForgotPasswordArgs & {
            confirmPassword: string
        },
    ) => Promise<void>
}
export const PasswordResetPageView: React.FC<PasswordResetPageViewProps> = ({
    onSubmit,
}) => {
    const classes = useStyles()

    return (
        <Container component="main" maxWidth="xs">
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Change Password
                </Typography>

                <FormComponent
                    submitButtonTitle="Change Password"
                    formItems={formItems}
                    onSubmit={onSubmit}
                />
            </div>
        </Container>
    )
}

export interface PasswordResetPageProps {}
export const PasswordResetPage: React.FC<PasswordResetPageProps> = () => {
    const {updateSnack} = useSnack()

    const [mutate] = useSubmitForgotPasswordMutation({
        onCompleted: (data) => {
            if (data.submitForgotPassword) {
                updateSnack({
                    message: "successfully changed password. redirecting...",
                    severity: "success",
                })
                setTimeout(() => {
                    goTo("/account/login")
                }, 1000)
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
    })

    const query = useQuery()
    const argToken = useMemo(() => query.get("token"), [query])

    const onSubmit = useCallback(
        async (
            args: MutationSubmitForgotPasswordArgs & {
                confirmPassword: string
            },
        ) => {
            if (args.password === args.confirmPassword && argToken) {
                mutate({
                    variables: {
                        email: args.email,
                        password: args.confirmPassword,
                        token: argToken,
                    },
                })
            } else {
                updateSnack({message: "passwords do not match"})
            }
        },
        [argToken, mutate, updateSnack],
    )

    return (
        <PageComponent
            showFooter
            title="Password Reset"
            rolePages={[
                {redirect: "/", role: "Submitter"},
                {redirect: "/", role: "Volunteer"},
            ]}>
            {argToken ? (
                <PasswordResetPageView onSubmit={onSubmit} />
            ) : (
                <WrongPageComponent text="Invalid reset token" />
            )}
        </PageComponent>
    )
}
