import {
    createStyles,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    makeStyles,
    Theme,
} from "@material-ui/core"
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction"
import * as clipboard from "clipboard-polyfill"
import React from "react"
import {useSnack} from "../hooks"

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        list: {
            backgroundColor: theme.palette.background.paper,
        },
    }),
)

export interface InfoListItem {
    action:
        | {type: "copy"; value?: string} // if value is undefined used then copy description
        | {callback: (id?: string) => void; type: "callback"}
    actionItem?: JSX.Element
    description: string | number | Date
    id?: string
    image?: string
    name: string
}

export interface InfoListComponentProps {
    items: InfoListItem[]
    style?: React.CSSProperties
}
export const InfoListComponent: React.FC<InfoListComponentProps> = ({
    items,
    style,
}) => {
    const classes = useStyles()
    const {updateSnack} = useSnack()

    return (
        <List
            component="nav"
            aria-labelledby="nested-list-subheader"
            style={style}
            className={classes.list}>
            {items.map(({action, actionItem, description, id, image, name}) => {
                return (
                    <ListItem
                        button
                        onClick={() => {
                            if (action.type === "copy") {
                                clipboard.writeText(
                                    action.value || String(description),
                                )

                                updateSnack({
                                    message: "copied to clipboard",
                                    severity: "info",
                                })
                            } else {
                                action.callback(id)
                            }
                        }}
                        key={name}>
                        {image && (
                            <ListItemAvatar>
                                <img
                                    style={{
                                        // height: 150,
                                        marginRight: 10,
                                        width: 150,
                                    }}
                                    src={image}
                                    alt={image}
                                />
                            </ListItemAvatar>
                        )}
                        <ListItemText
                            primaryTypographyProps={{style: {fontSize: "120%"}}}
                            primary={name}
                            secondary={String(description)}
                        />
                        {actionItem && (
                            <ListItemSecondaryAction>
                                {actionItem}
                            </ListItemSecondaryAction>
                        )}
                    </ListItem>
                )
            })}
        </List>
    )
}
