import {
    ButtonBase,
    createStyles,
    makeStyles,
    Theme,
    Typography,
} from "@material-ui/core"
import React from "react"

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        focusVisible: {},
        image: {
            "&:hover, &$focusVisible": {
                "& $imageBackdrop": {
                    opacity: 0.5,
                },
                "& $imageMarked": {
                    opacity: 0,
                },
                "& $imageTitle": {
                    border: "4px solid currentColor",
                },
                zIndex: 1,
            },
            height: "40vh",
            [theme.breakpoints.down("xs")]: {
                height: 100, // Overrides inline-style
                width: "100% !important",
            },
            position: "relative",
        },
        imageBackdrop: {
            backgroundColor: theme.palette.common.black,
            bottom: 0,
            left: 0,
            opacity: 0.6,
            position: "absolute",
            right: 0,
            top: 0,
            transition: theme.transitions.create("opacity"),
        },
        imageButton: {
            alignItems: "center",
            bottom: 0,
            color: theme.palette.common.white,
            display: "flex",
            fontWeight: 50,
            justifyContent: "center",
            left: 0,
            position: "absolute",
            right: 0,
            top: 0,
        },
        imageMarked: {
            backgroundColor: theme.palette.common.white,
            bottom: -2,
            height: 3,
            left: "calc(50% - 9px)",
            position: "absolute",
            transition: theme.transitions.create("opacity"),
            width: 18,
        },
        imageSrc: {
            backgroundPosition: "center 40%",
            backgroundSize: "cover",
            bottom: 0,
            left: 0,
            position: "absolute",
            right: 0,
            top: 0,
        },
        imageTitle: {
            padding: `${theme.spacing(2)}px ${theme.spacing(4)}px ${
                theme.spacing(1) + 6
            }px`,
            position: "relative",
        },
        root: {
            display: "flex",
            flexWrap: "wrap",
            minWidth: 300,
            width: "100%",
        },
    }),
)

export interface PictureButtonProps {
    onClick: () => void
    title: string
    url: string
    width: string
}
export const PictureButton: React.FC<PictureButtonProps> = ({
    onClick,
    title,
    url,
    width,
}) => {
    const classes = useStyles()

    return (
        <ButtonBase
            onClick={onClick}
            focusRipple
            key={title}
            className={classes.image}
            focusVisibleClassName={classes.focusVisible}
            style={{
                height: 200,

                width,
            }}>
            <span
                className={classes.imageSrc}
                style={{
                    backgroundImage: `url(${url})`,
                }}
            />
            <span className={classes.imageBackdrop} />
            <span className={classes.imageButton}>
                <Typography
                    component="span"
                    variant="h4"
                    color="inherit"
                    className={classes.imageTitle}>
                    {title}
                    <span className={classes.imageMarked} />
                </Typography>
            </span>
        </ButtonBase>
    )
}
