const useStyles = makeStyles(() =>
    createStyles({
        submissionPageView: {
            maxWidth: 1200,
            minWidth: 500,
            textAlign: "center",
            width: "50vw",
        },

        viewOriginalSubmissionButton: {marginBottom: 35},
    }),
)

const headCells: HeadCell<Job>[] = [
    {
        disablePadding: true,
        id: "id",
        label: "Job ID",
        numeric: false,
    },
    {disablePadding: true, id: "quantity", label: "Quantity", numeric: true},
    {disablePadding: false, id: "progress", label: "Progress", numeric: true},
    {disablePadding: false, id: "type", label: "Type", numeric: true},
]

export interface SubmissionInfoProps {
    submission: Submission
}
export const SubmissionInfo: React.FC<SubmissionInfoProps> = ({
    submission: {
        address,
        createdAt,
        description,
        distance,
        files: {assemblyUrl},
        id,
        quantity,
        submitter,
    },
}) => {
    const date = useDateFromNow(createdAt)
    return (
        <InfoListComponent
            items={
                [
                    {
                        action: {type: "copy"},
                        description: id,
                        name: "Id",
                    },
                    {
                        action: {type: "copy"},
                        description: quantity,
                        name: "Quantity",
                    },
                    {
                        action: {type: "copy"},
                        description: date,
                        name: "Created",
                    },
                    {
                        action: {type: "copy"},
                        description: description ?? "N/A",
                        name: "Description",
                    },

                    {
                        action: {
                            callback: () => {
                                if (assemblyUrl) {
                                    goToNewTab(assemblyUrl)
                                }
                            },
                            type: "callback",
                        },
                        description: assemblyUrl || "N/A",
                        name: "Assembly Information",
                    },

                    {
                        action: {type: "copy", value: submitter.email},
                        description: `${submitter.firstName} ${submitter.lastName} <${submitter.email}>`,
                        name: "Submitter",
                    },
                    {
                        action: {type: "copy"},
                        description: address,
                        name: `Shipping Address (Distance: ${distance.toFixed(
                            1,
                        )} miles)`,
                    },
                ] as InfoListItem[]
            }
        />
    )
}

const useMyUserType = () => {
    const {data, loading} = useMeQuery()
    if (loading) {
        return undefined
    }
    assert(data?.me, `Couldn't load my user`)
    return data.me.user.type
}

export interface SubmissionActionsProps {
    submission: Submission
}
export const SubmissionActions: React.FC<SubmissionActionsProps> = ({
    submission,
}) => {
    const {id, name} = submission
    const userType = useMyUserType()

    const {updateSnack} = useSnack()
    const [cancel] = useCancelSubmissionMutation({
        onCompleted: (cancelResponse) => {
            if (cancelResponse.cancelSubmission) {
                goTo("/")
            }
        },
        onError: ({message}) => {
            updateSnack({message})
        },
        variables: {id},
    })
    const onRemoveSubmission = useCallback(() => {
        // eslint-disable-next-line no-alert
        if (!confirm(`Are you sure you want to remove submission "${name}"?`)) {
            return
        }
        cancel()
    }, [cancel, name])

    switch (userType) {
        case "Submitter":
            return (
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={onRemoveSubmission}>
                    Remove Submission
                </Button>
            )
        case "Volunteer":
            return <CreateNewJobModal {...{submission}} />
        default:
            return <div>Loading...</div>
    }
}

export interface SubmissionTitleProps {
    submission: Submission
}
export const SubmissionTitle: React.FC<SubmissionTitleProps> = ({
    submission: {
        createdAt,
        files: {preview},
        name,
    },
}) => {
    const date = useDateFromNow(createdAt)

    return (
        <div>
            <Typography component="h5" variant="h5">
                {name}
            </Typography>
            <Typography variant="subtitle1">{date}</Typography>
            {preview && (
                <CardMedia
                    style={{borderRadius: 20, marginTop: 10}}
                    component="img"
                    image={preview.presignedUrl}
                    title={name}
                />
            )}
        </div>
    )
}

export interface SubmissionPageViewProps {
    showJobs?: boolean
    submission: Submission
}
export const SubmissionPageView: React.FC<SubmissionPageViewProps> = ({
    showJobs = false,
    submission,
}) => {
    const classes = useStyles()

    return (
        <div className={classes.submissionPageView}>
            <Card>
                <CardContent>
                    <SubmissionTitle {...{submission}} />

                    {showJobs && <SubmissionJobs {...{submission}} />}
                    <FileItemPreview
                        items={[
                            ...submission.files.modelFiles.map((file) => {
                                return {file, type: "stl" as "stl"}
                            }),
                            ...submission.files.laserCuttingFiles.map(
                                (file) => {
                                    return {file, type: "dxf" as "dxf"}
                                },
                            ),
                        ]}
                    />
                    <SubmissionInfo {...{submission}} />
                    <SubmissionActions {...{submission}} />
                </CardContent>
            </Card>
        </div>
    )
}

export interface SubmissionPageProps {}
export const SubmissionPage: React.FC<SubmissionPageProps> = () => {
    const {updateSnack} = useSnack()
    const id = useQuery().get("id")

    assert(id, `Submission id must be set.`)

    const {data} = useSubmissionQuery({
        onError: ({message}) => {
            updateSnack({message})
        },
        variables: {id},
    })

    const submissionPage = useCallback(
        (userType: UserType, showJobs?: boolean): RolePage => ({
            component: () => (
                <div style={{display: "flex", justifyContent: "center"}}>
                    {data?.submission && (
                        <SubmissionPageView
                            submission={data?.submission}
                            {...{showJobs}}
                        />
                    )}
                </div>
            ),
            role: userType,
        }),
        [data],
    )

    return (
        <SubmissionFilesContainer.Provider>
            <PageComponent
                disableHide={false}
                showFooter
                title="Submission"
                rolePages={[
                    submissionPage("Submitter", true),
                    submissionPage("Volunteer"),
                ]}>
                <WrongPageComponent text="Must be signed in to view Job" />
            </PageComponent>
        </SubmissionFilesContainer.Provider>
    )
}

const SubmissionFilesContainer = createContainer(() => {
    const [jobType, setJobType] = useState<JobType>("Printing")

    return {jobType, setJobType}
})

export interface SubmissionSelectorProps {
    label: string
    submission: Submission
}
export const SubmissionSelector: React.FC<SubmissionSelectorProps> = ({
    label,
    submission,
}) => {
    const {jobType} = useContainer(SubmissionFilesContainer)

    const [files, renderType] = useMemo(() => {
        switch (jobType) {
            case "LaserCutting":
                return [submission.files.laserCuttingFiles, "dxf"] as const
            case "Printing":
                return [submission.files.modelFiles, "stl"] as const
            default:
                return [[] as File[], undefined] as const
        }
    }, [submission.files, jobType])

    if (renderType === undefined) {
        return null
    }

    return (
        <>
            <Typography gutterBottom>{label}</Typography>
            <FileItemPreview
                items={files.map((file) => {
                    return {file, type: renderType}
                })}
            />
        </>
    )
}

/**
 * TODO: There is currently a bug that exists which is not worth fixing at the moment.
 * When you create a job, the max value of the job type doesn't update.
 *
 * To reproduce, create a job from a submission where the # of laser cutting quantity needed is less than model quantity.
 * Then, go to the model quantity and set the quantity to max, then change the type of the job.
 * The internal state will hold the previous job type.
 */
export interface CreateNewJobModalProps {
    submission: Submission
}
export const CreateNewJobModal: React.FC<CreateNewJobModalProps> = ({
    submission,
}) => {
    const {jobType, setJobType} = useContainer(SubmissionFilesContainer)

    const laserCutterOption = submission.files.laserCuttingFiles?.length
        ? {LaserCutting: "Laser Cutting"}
        : {}
    const printingOption = submission.files.modelFiles?.length
        ? {Printing: "3D Printing"}
        : {}

    const maxQuantity = useMemo(() => {
        switch (jobType) {
            case "LaserCutting":
                return submission.progress.laserCuttingQuantity
            case "Printing":
                return submission.progress.modelQuantity
            default:
                return submission.quantity
        }
    }, [jobType, submission.progress, submission.quantity])
    const submitFormItems = useMemo<FormItem[]>(
        () =>
            [
                {
                    alignment: {xs: 12},
                    defaultValue: submission.files.laserCuttingFiles?.length
                        ? "LaserCutting"
                        : "Printing",
                    id: ".type",
                    label: "Job Type",
                    options: {
                        ...laserCutterOption,
                        ...printingOption,
                    },
                    required: true,
                    type: "select",
                },
                {
                    alignment: {xs: 12},
                    id: ".models",
                    label: "Model Preview...",
                    render: ({label}) => (
                        <SubmissionSelector {...{label, submission}} />
                    ),
                    type: "custom",
                },
                {
                    alignment: {xs: 12},
                    defaultValue: 5,
                    ending: "Parts",
                    id: ".quantity",
                    label: "Quantity",
                    max: maxQuantity,
                    min: 1,
                    step: 1,
                    type: "slider",
                },
            ] as FormItem[],
        [laserCutterOption, maxQuantity, printingOption, submission],
    )

    const [mutate] = useCreateNewJobFromSubmissionMutation({
        onCompleted: ({createNewJobFromSubmission: {id}}) => {
            goTo(`/job/?id=${id}`)
        },
    })
    const onSubmit = useCallback(
        ({quantity}: {quantity: number}) => {
            assert(jobType, `Job type must be set!`)

            mutate({
                variables: {
                    id: submission.id,
                    quantity,
                    type: jobType,
                },
            })
        },
        [jobType, mutate, submission.id],
    )

    const onUpdate = useCallback(
        (id: string, value: unknown) => {
            if (id !== ".type" || !value) {
                return
            }
            assert(
                value === "LaserCutting" || value === "Printing",
                `Invalid job type: ${value}`,
            )

            setJobType(value)
        },
        [setJobType],
    )

    return (
        <ModalComponent title="Create New Job">
            <FormComponent
                submitButtonTitle="Create New Job"
                formItems={submitFormItems}
                {...{onSubmit, onUpdate}}
            />
        </ModalComponent>
    )
}

export interface MarkAsReceivedButtonProps {
    id: string
    progress: JobProgress
}
export const MarkAsReceivedButton: React.FC<MarkAsReceivedButtonProps> = ({
    id,
    progress,
}) => {
    const {updateSnack} = useSnack()
    const [mutate] = useToggleJobReceivedMutation({
        onError: ({message}) => {
            updateSnack({message})
        },
        variables: {id},
    })

    return (
        <Button
            variant="contained"
            color={progress === "Shipped" ? "primary" : "secondary"}
            onClick={() => mutate()}>
            {progress === "Shipped" ? "Received" : "Undo"}
        </Button>
    )
}

export interface SubmissionJobsProps {
    submission: Submission
}
export const SubmissionJobs: React.FC<SubmissionJobsProps> = ({
    submission: {id, name},
}) => {
    const {data, loading} = useSubmissionJobsQuery({variables: {id}})
    if (loading) {
        return <div>Loading...</div>
    }
    assert(data?.submissionJobs, `Couldn't get submitted jobs!`)

    return (
        <div style={{marginTop: 20}}>
            <EnhancedTable
                initialOrderProperty="createdAt"
                nameProperty="id"
                customItems={[
                    ({id, progress}: ComparableObject) => {
                        assert(typeof id === "string", "invalid id")
                        assert(typeof progress === "string", "invalid progress")

                        if (progress === "Shipped" || progress === "Received") {
                            return <MarkAsReceivedButton {...{id, progress}} />
                        }
                        return null
                    },
                ]}
                toolbarBarItems={[
                    <Button
                        key="export"
                        onClick={() => {
                            downloadString(
                                jsonToCsv(data.submissionJobs, (value) => {
                                    return value?.firstName && value?.lastName
                                        ? `${value.firstName} ${String(
                                              value.lastName.charAt(0),
                                          ).toUpperCase()}`
                                        : "INFORMATION_REDACTED"
                                }),
                                "",
                                `${id}-${name}-JobExport.csv`,
                            )
                        }}
                        style={{
                            fontSize: 12,
                            height: 35,
                            width: 200,
                        }}
                        variant="contained"
                        startIcon={<CloudDownloadIcon />}
                        color="default">
                        Export Jobs
                    </Button>,
                ]}
                rows={(data.submissionJobs as unknown) as ComparableObject[]}
                headCells={
                    (headCells as unknown) as HeadCell<ComparableObject>[]
                }
                onRowClick={(row) => {
                    goTo(`/job?id=${row.id}`)
                }}
                title="Accepted Jobs"
            />
        </div>
    )
}
