import {Grow, makeStyles, Paper} from "@material-ui/core"
import * as React from "react"
import {useEffect, useState} from "react"
import ReactMarkdown from "react-markdown/with-html"

const useStyles = makeStyles(() => ({
    container: {
        display: "flex",
        justifyContent: "center",
    },

    paper: {
        marginTop: "1vh",
        maxWidth: 800,
        width: "90vw",
    },
}))

export interface MarkdownComponentProps {
    onFinish?: () => void
    url: string
    variableMaps?: {from: string | RegExp; to: string}[]
}
export const MarkdownComponent: React.FC<MarkdownComponentProps> = ({
    url: fileName,
    onFinish,
    variableMaps = [],
}) => {
    const classes = useStyles()

    const [content, changeContent] = useState<string>()

    useEffect(() => {
        ;(async () => {
            changeContent(
                variableMaps.reduce(
                    (acc, {from, to}) => acc.replace(from, to),
                    await (await fetch(`/assets/docs/${fileName}`)).text(),
                ),
            )
        })()
    }, [fileName, variableMaps])

    return (
        <div className={classes.container}>
            <Grow onEntered={onFinish} timeout={1000} in>
                <Paper className={classes.paper}>
                    <div style={{padding: 25}}>
                        {content ? (
                            <ReactMarkdown
                                escapeHtml={false}
                                source={content}
                            />
                        ) : null}
                    </div>
                </Paper>
            </Grow>
        </div>
    )
}
