import {Logging} from "@nimashoghi/logging"
import React, {useEffect, useState} from "react"
import {dxfToImageBuffer} from "../services/thumbnail"
import {contentFromFile} from "../services/utils"

const logger = Logging.logger({__filename})

export interface DXFViewerProps {
    height: number
    source:
        | {type: "url"; url: string}
        | {data: string; type: "data"}
        | {file: File; type: "file"}
    strokeColor?: string
    strokeWidth?: string
    width: number
}
export const DXFViewer: React.FC<DXFViewerProps> = ({
    height,
    source,
    strokeColor,
    strokeWidth,
    width,
}) => {
    const [base64, changeSBase64] = useState<string>()

    useEffect(() => {
        const updateSvg = (dxf: string) => {
            try {
                // eslint-disable-next-line no-control-regex
                const isAscii = /^[\x00-\x7F]*$/.test(dxf)
                if (isAscii) {
                    const buff = dxfToImageBuffer({
                        dxf,
                        strokeColor,
                        strokeWidth,
                    })
                    const base64data = buff.toString("base64")
                    changeSBase64(base64data)
                }
            } catch (error) {
                logger.debug`Got error ${error}... Ignoring`
            }
        }

        const getDXFData = async () => {
            if (source.type === "data") {
                updateSvg(source.data)
            } else if (source.type === "file") {
                contentFromFile({
                    file: source.file,
                    onLoad: (content) => {
                        updateSvg(content)
                    },
                })
            } else {
                logger.debug`${source.url}`
                const response = await fetch(source.url)
                const dxf = await response.text()
                updateSvg(dxf)
            }
        }
        getDXFData()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <div style={{height, width}}>
            {base64 ? (
                <div style={{padding: width * 0.08}}>
                    <img src={`data:image/svg+xml;base64,${base64}`} alt="" />
                </div>
            ) : null}
        </div>
    )
}
