/* eslint-disable @typescript-eslint/no-explicit-any */

import {Button, Grid, makeStyles} from "@material-ui/core"
import React, {useCallback} from "react"
import {IPreviewProps} from "react-dropzone-uploader"
import {useForm} from "../../hooks/useForm"
import {FormItemResolverComponent} from "./resolver"

export type ReactComponent<Props> = (
    props: Props,
) => React.ReactNode | React.Component<Props>

export interface FormItemBase {
    alignment: {sm?: 12 | 6; xs?: 12 | 6}
    autoComplete?: string
    defaultValue?: any
    hint?: string
    id: string
    label: string
    multiline?: boolean
    required?: boolean
}

export interface TextFormItem extends FormItemBase {
    textType?: string
    type: "text"
}

export interface LocationFormItem extends FormItemBase {
    freeSolo?: boolean
    locationIds?: {
        latitude: string
        longitude: string
    }
    textType?: string
    type: "location"
}

export interface SliderFormItem extends FormItemBase {
    ending?: string
    max: number
    min: number
    step: number
    type: "slider"
}

export interface SelectFormItem extends FormItemBase {
    disabled?: boolean
    options: {[value: string]: string}
    type: "select"
}

export interface FileUploadFormItem extends FormItemBase {
    PreviewComponent?: ReactComponent<
        IPreviewProps & {
            handleChange: (value: File[] | File | undefined) => void
            id: string
        }
    >
    accept?: string
    icon?: JSX.Element
    multiple?: boolean
    type: "file-upload"
}

export interface CustomFormItem extends FormItemBase {
    render: (props: FormItemBase) => JSX.Element
    type: "custom"
}

export type FormItem =
    | TextFormItem
    | SelectFormItem
    | SliderFormItem
    | LocationFormItem
    | FileUploadFormItem
    | CustomFormItem

const useStyles = makeStyles((theme) => ({
    form: {
        marginTop: theme.spacing(3), // Fix IE 11 issue.
        width: "100%",
    },
    spacing: {
        marginTop: 30,
    },
}))

export interface FormComponentProps<T> {
    children?: JSX.Element | JSX.Element[]
    formItems: FormItem[]
    initialForm?: Partial<T>
    onSubmit: (form: T) => void
    onUpdate?: (id: string, value: any) => void
    submitButtonTitle: string
}
export function FormComponent<T>({
    children,
    formItems,
    initialForm,
    onSubmit,
    onUpdate,
    submitButtonTitle,
}: FormComponentProps<T>) {
    const classes = useStyles()
    const {form, updateForm} = useForm<T>(initialForm)

    const update = useCallback(
        (id: string, value: any) => {
            updateForm(id, value)
            if (onUpdate) {
                onUpdate(id, value)
            }
        },
        [updateForm, onUpdate],
    )

    return (
        <form
            className={classes.form}
            onSubmit={(event) => {
                event.preventDefault()
                onSubmit(form as T)
            }}>
            <Grid container spacing={2}>
                {formItems.map((formItem) => {
                    const {sm, xs} = formItem.alignment
                    return (
                        <Grid item xs={xs} sm={sm} key={formItem.id}>
                            <FormItemResolverComponent
                                handleChange={update}
                                {...{form}}
                                {...formItem}
                            />
                        </Grid>
                    )
                })}
            </Grid>
            {children}
            <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.spacing}>
                {submitButtonTitle}
            </Button>
        </form>
    )
}
